import { Component, OnInit } from '@angular/core';
import { BsModalRef, BsModalService } from 'ngx-bootstrap/modal';
import { Subject } from 'rxjs';
import * as XLSX from 'xlsx';

@Component({
  selector: 'app-upload-desk-review',
  templateUrl: './upload-desk-review.component.html',
  styleUrls: ['./upload-desk-review.component.scss']
})
export class UploadDeskReviewComponent implements OnInit {
  incomingData:any;
  data: any;
  thead: any;
  tbody: any;

  constructor(public activeModal:BsModalRef) { }

  ngOnInit() {
  }

  dropHandler(ev) {
    // Prevent default behavior (Prevent file from being opened)
    //console.log(ev);
    
    ev.preventDefault();

    if (ev.dataTransfer.items) {
      // Use DataTransferItemList interface to access the file(s)
      for (var i = 0; i < ev.dataTransfer.items.length; i++) {
        // If dropped items aren't files, reject them
        if (ev.dataTransfer.items[i].kind === 'file') {
          var file = ev.dataTransfer.items[i].getAsFile();
          // this.fileArray.push({
          //   fileObj: file
          // });
          this.onFileChange(file)
          // //console.log('... file[' + i + '].name = ' + file.name);
        }
      }
    } else {
      // Use DataTransfer interface to access the file(s)
      for (var i = 0; i < ev.dataTransfer.files.length; i++) {
        // //console.log('... file[' + i + '].name = ' + ev.dataTransfer.files[i].name);
      }
    }
  }

  dragOverHandler(ev) {
    // Prevent default behavior (Prevent file from being opened)
    ev.preventDefault();
  }

  onFileChange(evt: any) {
    this.tbody = []
    const reader: FileReader = new FileReader();
    reader.onload = (e: any) => {
      /* read workbook */
      const bstr: string = e.target.result;
      const wb: XLSX.WorkBook = XLSX.read(bstr, { type: 'binary' });

      /* grab first sheet */
      const wsname: string = wb.SheetNames[0];
      const ws: XLSX.WorkSheet = wb.Sheets[wsname];

      //console.log(wb.SheetNames);
      
      /* save data */
      this.data = XLSX.utils.sheet_to_json(ws, { header: 1 });
      
      // this.thead = this.data[0]
      // for (let i = 0; i < this.thead.length; i++) {
      //   this.tbody.push
      // }
      

      for (let i = 1; i < this.data.length; i++) {
        this.tbody.push(this.data[i]);
      }
      //console.log(this.tbody)
      
    };
    reader.readAsBinaryString(evt);
  }

  
}
