import { Component, OnInit, Input } from '@angular/core';
import { Router } from '@angular/router';
import { BsModalRef } from 'ngx-bootstrap/modal';
import { ToastrService } from 'ngx-toastr';
import { CrudService } from 'src/app/service/crud.service';
import gql from 'graphql-tag';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Subject } from 'rxjs';


@Component({
  selector: 'app-create-leading',
  templateUrl: './create-leading.component.html',
  styleUrls: ['./create-leading.component.css']
})
export class CreateLeadingComponent implements OnInit {
  // @Input() data;
  data: any;

  postData: any = { states: [] };
  submit: any;
  sh: any;
  states: any;
  title;
  userObj: any;
  onClose: any;
  mdas: any;
  tree: any;

  constructor(private toastr: ToastrService, public activeModal: BsModalRef, private router: Router, private crudService: CrudService) {
  }

  ngOnInit() {
    this.title = this.data.title;
    this.onClose = new Subject();
    this.mdas = JSON.parse(localStorage.getItem('mdas'))

  }
  create(data) {
    this.submit = true;
    this.postData.priorityId = this.data.priorityId;
    this.postData.driverId = this.data.driverId;
    this.postData.mandateId = this.data.mandateId;
    let target = [];
    target.push({ year: 2020, target: this.postData.targetYear1 })
    target.push({ year: 2021, target: this.postData.targetYear2 })
    target.push({ year: 2022, target: this.postData.targetYear3 })
    target.push({ year: 2023, target: this.postData.targetYear4 })
    this.postData.target = JSON.stringify(target);

    delete this.postData.targetYear1;
    delete this.postData.targetYear2;
    delete this.postData.targetYear3;
    delete this.postData.targetYear4;
    delete this.postData.states;

    let query = gql`

   mutation createLeading($indicatorInput: indicatorInputType!) {
      createLeading(indicatorInput: $indicatorInput) {
        id
      
      }
      }
    
    `
    //     let itemObj = {
    //       title: data.value.name,
    //       baseline: data.value.baselineInput,
    //       actual: data.value.actualInput,
    //       target: data.value.targetInput,
    //       aggregation: data.value.aggregation,
    //       rt: data.value.rt,
    //       priorityId: this.data.id,
    // // 
    //     }
    this.crudService.postData(query, { indicatorInput: this.postData })
      .subscribe(
        (res: any) => {

          this.toastr.success(" Leading Indicator successful Created", "Success")
          this.activeModal.hide()
          this.onClose.next(false);
        },
        (error) => {
          this.toastr.error(" Leading Indicator created unsuccessly, kindly try again", "Unsuccessful")
        });
  }

  getUser() {
    this.userObj = JSON.parse(localStorage.getItem('user'))
    return this.userObj
  }

  calculateTarget() {
    //console.log('calculating')
    if (this.postData.type == 'quan') {
      let base = (parseInt(this.postData.targetYear1) - parseInt(this.postData.baseline)) / 4;
      //console.log(base);

      this.postData.targetYear1 = parseInt(this.postData.baseline) + (base * 1);
      this.postData.targetYear2 = parseInt(this.postData.baseline) + (base * 2);
      this.postData.targetYear3 = parseInt(this.postData.baseline) + (base * 3);
      this.postData.targetYear4 = parseInt(this.postData.baseline) + (base * 4);
      //console.log(parseInt(this.postData.baseline) + (base * 4));

    }
  }
}
