import { Subject } from 'rxjs';
import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import gql from 'graphql-tag';
import { CrudService } from 'src/app/service/crud.service';
import * as XLSX from 'xlsx';
import { BsModalRef } from 'ngx-bootstrap';
import { ToastrService } from 'ngx-toastr';
declare var $: any;

@Component({
  selector: 'app-budget-management',
  templateUrl: './budget-management.component.html',
  styleUrls: ['./budget-management.component.scss']
})
export class BudgetManagementComponent implements OnInit {
  uploadForm: FormGroup;
  tbody: any[];
  filedata: any;
  mda: any;
  rowIndex: any;
  excelFile: any;
  p: any;

  public facilityForm: FormGroup;
  public budgetForm: FormGroup;
  public leadForm: FormGroup;
  healthFacilityClassification;
  healthFacilityLga;
  active = 1;
  completed = 0;
  current = 1;
  value;
  elementType;
  ministries: any;
  fileName: any;
  ministryName: any;
  selectedDate: any = new Date().getFullYear();
  uploaded: number = 0;

  year = [];
  colorType: string = 'success'
  onClose: Subject<{}>;
  err: boolean;
  constructor(private fb: FormBuilder, private crud: CrudService, private toastr: ToastrService, public activeModal: BsModalRef, private route: Router) {
    this.getAllMda();
    this.facilityForm = new FormGroup({
      'classification': new FormControl(null, Validators.required),
      'selectedYear': new FormControl(null, Validators.required),
    });
    this.budgetForm = new FormGroup({
      'budget': new FormControl(null, Validators.required)
    });

    let year = new Date().getFullYear();
    year = year + 1;
    // this.selectedDate = localStorage.getItem('activeYear');
    for (let index = 0; index < 10; index++) {
      this.year.push((year - index) + '');
    }
    console.log(this.year);
    this.onClose = new Subject();

  }

  ngOnInit() {
  }

  onFileChange() {
    this.tbody = [];
    console.log(this.excelFile.target);

    const target: DataTransfer = <DataTransfer>(this.excelFile.target);
    if (target.files.length !== 1) throw new Error('Cannot use multiple files');

    const reader: FileReader = new FileReader();
    this.fileName = this.excelFile.target.files[0].name;

    reader.onload = (e: any) => {
      /* read workbook */
      const bstr: string = e.target.result;
      const wb: XLSX.WorkBook = XLSX.read(bstr, { type: 'binary' });

      /* grab first sheet */
      const wsname: string = wb.SheetNames[0];
      const ws: XLSX.WorkSheet = wb.Sheets[wsname];

      /* save data */
      this.filedata = XLSX.utils.sheet_to_json(ws, { header: 1 });
      this.filedata.forEach(e => {
        this.tbody.push({
          budget_code: e[0],
          budget_line_item: e[1],
          amount: e[2],
        });
      });
    };
    reader.readAsBinaryString(target.files[0]);

    this.submitOwnerForm();
  }

  openRowDeleteModal(i) {
    this.rowIndex = i;
    this.tbody.splice(this.rowIndex, 1);
    // $('#deleteRowModal').modal('show');
  }

  setYear(event) {
    this.selectedDate = event.target.value;
  }

  get classification() {
    return this.facilityForm.get('classification');
  }
  get selectedYear() {
    return this.facilityForm.get('selectedYear');
  }
  get owner_name() {
    return this.budgetForm.get('owner_name');
  }


  submitFacilityForm() {
    this.active++;
    this.completed++;
    this.current++;

    this.ministryName = this.ministries.filter((e) =>
      e.id === this.facilityForm.value.classification);
  }

  submitOwnerForm() {
    this.active++;
    this.completed++;
    this.current++;
  }

  async submitLeadForm() {
    let allData = { ...this.facilityForm.value, ...this.budgetForm.value };
    try {
      let facility = await this.crud.postData(allData, 'facility-registration');
      this.facilityForm.reset();
      this.budgetForm.reset();
      this.leadForm.reset();
      this.active = this.active - 2;
      this.completed = this.completed - 2;
      this.current = this.current - 2;
      // this.toastr.success('Registration Successful', 'Process Registration')
      //console.log('Registration Successful');
    } catch (error) {
      // this.toastr.error('Registration Failed', 'Process Registration')
      //console.log('Registration Failed');
    }
  }

  setActive() {
    this.active--;
    this.completed--;
    this.current--;
  }

  getAllMda() {
    const data = gql`
      {
        mdas{
          id
          name
          shortcode
        }
      }
    `;
    this.crud.pullData(data, {})
      .subscribe((result: any) => {
        this.ministries = result.data.mdas;
      });
  }
  async beginUpload() {
    // this.active++;
    // this.completed++;
    this.current = 4;
    this.active = 4;

    this.upload();

  }
  async upload() {
    console.log(this.current);
    let data = []
    for await (const bud of this.tbody) {
      let err
      data.push(bud);
      if (data.length == 5 || data.length == this.tbody.length - this.uploaded) {

        await this.uploadBudget(data).then(() => {
          this.uploaded += data.length;
          data = [];
          console.log(this.uploaded);

        }, () => {
          this.err = true;
          // err = true;
          this.colorType = 'danger';
        });
      }
      if (this.err) break;
    }
  }
  cancelUpload() {
    this.err = true;
    this.setActive();
  }
  finish() {
    // this.cancelUpload();
    this.onClose.next(false);
    this.activeModal.hide()
  }
  uploadBudget = (data) => {
    return new Promise((resolve, reject) => {
      const query = gql`
    mutation createBudget(
      $budget_data: String!, $year: String!, $mdaId: String!
      ){
        createBudget(budget_data: $budget_data, year: $year, mdaId: $mdaId) {
          status
        }
      }
    `;

      let obj = {
        budget_data: JSON.stringify(data),
        year: this.facilityForm.value.selectedYear,
        mdaId: this.facilityForm.value.classification
      }
      this.crud.postData(query, obj).subscribe((result) => {
        if (!result.data['createBudget']) {
          // $('#failureModal').modal('show');
          reject(Error("It broke"));
          this.toastr.error("Something went wrong", "Error")


        } else {
          resolve("Stuff worked!");
          // $('#successModal').modal('show');
          // setTimeout(() => {
          //   $('#successModal').modal('hide');
          //   this.route.navigate(['home/budget-presentation'])
          // }, 2000);
        }
      }, (error) => {
        // $('#failureModal').modal('show');
        reject(Error("It broke"));
      });


    });

  }
}
