import { Subject } from 'rxjs';
import { filter } from 'rxjs/operators';
import { Component, OnInit, Input } from '@angular/core';




import { ToastrService } from 'ngx-toastr';
import { Router } from '@angular/router';
import { BsModalRef } from 'ngx-bootstrap/modal';
import { ENGINE_METHOD_DIGESTS } from 'constants';
import gql from 'graphql-tag';
import { CrudService } from '../../service/crud.service';

@Component({
  selector: 'app-create-initiative',
  templateUrl: './create-initiative.component.html',
  styleUrls: ['./create-initiative.component.css']
})
export class CreateInitiativeComponent implements OnInit {
  @Input() data;
  postData: any = { fecId: '' };
  submit: any;
  sh: any;



  initiatives: any = [];
  onClose: any;
  help: any = [
    "Ensure you fill all fields",
    "You can tie the initiative to FEC memo now or at a later date"
  ]
  role: any;
  mda: any;
  constructor(private toastr: ToastrService, private crud: CrudService, public activeModal: BsModalRef, private router: Router) {
    if (JSON.parse(localStorage.getItem('initiatives')) != null)
      this.initiatives = JSON.parse(localStorage.getItem('initiatives'));

    this.role = JSON.parse(localStorage.getItem('role'));
    this.mda = JSON.parse(localStorage.getItem('mda'));
  }


  ngOnInit() {
    //console.log(this.data.title);

    this.onClose = new Subject();
  }

  create() {
    this.submit = true;

    this.postData.mdaId = this.mda.id;
    let query = gql`
      mutation createInitiative(
        $title: String!, $mdaId: ID!
        ){
          createInitiative(title: $title, mdaId: $mdaId) {
            id 
        }
        }
      `
    this.crud.postData(query, this.postData).subscribe((result) => {
      if (!result.data['createInitiative']) {
        // this.error = result.errors[0].message;

      } else {
        this.toastr.success("Initiative successful Created", "Success")
        this.onClose.next(false);
        this.activeModal.hide()
      }
    }, (error) => {
      this.toastr.error("Something went wrong, kindly try again", "Unsuccessful")
    });
  }
  // this.initiatives.push(this.postData);
  // localStorage.setItem('initiatives', JSON.stringify(this.initiatives))


  // getLga(id) {
  //   this.states.forEach(el => {
  //     if (el.state.name == this.postData.state)
  //       this.lgas = el.state.locals;
  //   });
  //   //console.log(this.lgas);

  // }
}
