import { Injectable } from '@angular/core';
import gql from 'graphql-tag';
import { CrudService } from './crud.service';
import { ToastrService } from 'ngx-toastr';
import { BehaviorSubject, Observable } from 'rxjs';
import { i18nMetaToJSDoc } from '@angular/compiler/src/render3/view/i18n/meta';

@Injectable({
  providedIn: 'root'
})
export class FunctionsService {
  activeYear;
  budget;
  uploadData = new BehaviorSubject({ loading: 'none', progress: 0, total: 0, type: 'Projects' });

  uploaded: number = 0;
  err: boolean;

  constructor(private crud: CrudService, private toastr: ToastrService) {
    this.activeYear = localStorage.getItem('activeYear');
  }

  getBudget() {
    const data = gql`
    query budgetByYear($year: String!) {
      budgetByYear(year: $year) {
          id
          budget_line_item
          budget_code
          amount
          year
          mda{
            id
            name
            shortcode
          }
        }
      }
    `;
    this.crud.pullData(data, { year: this.activeYear })
      .subscribe((result: any) => {
        this.budget = result.data.budgetByYear;
        console.log(this.budget);
      });
  }

  async upload(tbody, state) {
    let uploadObj = [];
    let batch = Math.floor(1000 + Math.random() * 9000)
    tbody.forEach(el => {
      if (el[0] != null || el[2] != null) {
        let uid
        if (el[1] == undefined) { uid = null }
        else { uid = el[1].replace(/ /g, '') }
        uploadObj.push({
          puid: uid,
          fecId: el[2],
          title: el[3],
          approvalDate: el[4],
          duuid: el[6],
          location: el[7],
          start_date: el[9],
          end_date: el[10],
          // longitude: el[5],
          // latitude: el[6],
          currency: el[12],
          cost: el[13],
          budget: el[13],
          variationCost: el[14],
          fundingSource: el[15],
          appropriated: el[16],
          released: el[17],
          utilized: el[18],
          status: el[19],
          planned_milestone: el[20],
          actual_milestone: el[21],
          loc: el[22],
          challenges: el[23],
          intervention: el[24],
          images: el[25],
          batch: batch

        })
      }

    });



    let data = []
    this.setInfo({ loading: 'true', progress: this.uploaded, total: uploadObj.length })


    for await (const bud of uploadObj) {
      let err
      data.push(bud);
      if (data.length == 5 || data.length == uploadObj.length - this.uploaded) {

        await this.submitDeskReview(data, state).then(() => {
          this.uploaded += data.length;
          if (uploadObj.length == this.uploaded)
            this.setInfo({ loading: 'false', progress: this.uploaded, total: uploadObj.length })


          else
            this.setInfo({ loading: 'true', progress: this.uploaded, total: uploadObj.length })
          data = [];
          console.log(this.uploaded);

        }, () => {
          this.err = true;
          // err = true;
          // this.colorType = 'danger';
        });
      }
      if (this.err) break;
    }
  }
  async uploadOutput(tbody, state) {
    console.log(tbody);
    console.log(state);

    let uploadObj = [];
    let batch = Math.floor(1000 + Math.random() * 9000)
    tbody.forEach(el => {
      if (el[0] != null || el[2] != null) {
        // let uid
        // if (el[1] == undefined) { uid = null }
        // else { uid = el[1].replace(/ /g, '') }
        uploadObj.push({
          // puid: uid,

          title: el[3],
          type: el[4],
          uuid: el[5],
          outputName: el[6],
          outputBaseline: el[8],
          outputActual: el[10],
          outputYear1: el[9],
          outputYear2: el[15],
          outputYear3: el[20],
          outputYear4: el[25],
        })
      }

    });



    let data = []
    this.setInfo({ loading: 'true', progress: this.uploaded, total: uploadObj.length })


    for await (const bud of uploadObj) {
      let err
      data.push(bud);
      if (data.length == uploadObj.length - this.uploaded) {

        await this.submitSubs(data, state).then(() => {
          this.uploaded += data.length;
          if (uploadObj.length == this.uploaded)
            this.setInfo({ loading: 'false', progress: this.uploaded, total: uploadObj.length, type: 'Subs' })


          else
            this.setInfo({ loading: 'true', progress: this.uploaded, total: uploadObj.length, type: 'Subs' })
          data = [];
          console.log(this.uploaded);

        }, () => {
          this.err = true;
          // err = true;
          // this.colorType = 'danger';
        });
      }
      if (this.err) break;
    }
  }
  submitDeskReview(data, state) {
    return new Promise((resolve, reject) => {

      let query = gql`
      mutation uploadLogframe($logframeData:String!, $projectData:String!, $mdaId: ID!, $report: String){
        uploadLogframe(logframeData:$logframeData, projectData:$projectData, mdaId: $mdaId, report: $report){
          status
        }
      }
    `
      // S / N	PROJECT UNIQUE ID	FEC ID.TITLE 	APPROVAL DATE	SIGNED MINISTERIAL DELIVERABLES / PRIORITY PROJECTS	LOCATION	GPS COORDINATES	COMMENCEMENT DATE	PLANNED COMPLETION DATE	PROJECT / PROGRAMME / POLICY(ONGOING)	PROJECT COST	SOURCE OF FUNDING	AMOUNT APPROPRIATED	AMOUNT RELEASED		AMOUNT UTILIZED		STATUS OF PROJECT		PLANNED MILESTONE 	ACTUAL MILESTONE	ACTUAL LEVEL OF WORK % CHALLENGES(IF ANY)	REQUIRED INTERVENTION(IF ANY)
      // PROJECT COST	AMOUNT APPROPRIATED	AMOUNT RELEASED		AMOUNT UTILIZED	%		STATUS OF PROJECT		ACTUAL MILESTONE %		ACTUAL LEVEL OF WORK %		CHALLENGES (IF ANY)	REQUIRED INTERVENTION(IF ANY)	


      this.crud.postData(query, { report: state.report, logframeData: JSON.stringify(data), projectData: JSON.stringify(data), mdaId: state.mda.id })
        .subscribe(res => {
          if (!res.errors) {
            // this.activeModal.hide()
            resolve('perfect')
            this.toastr.success('Upload Submitted Successfully', 'Desk Review')
          } else {
            // this.activeModal.hide()
            reject('oops')

            this.toastr.error(' Upload unsuccessful')
          }
        })
    });
  }
  submitSubs(data, state) {
    return new Promise((resolve, reject) => {

      let query = gql`
      mutation submitSubs($subData:String!, $mdaId: ID!){
        submitSubs(subData:$subData,  mdaId: $mdaId){
          status
        }
      }
    `
      // S / N	PROJECT UNIQUE ID	FEC ID.TITLE 	APPROVAL DATE	SIGNED MINISTERIAL DELIVERABLES / PRIORITY PROJECTS	LOCATION	GPS COORDINATES	COMMENCEMENT DATE	PLANNED COMPLETION DATE	PROJECT / PROGRAMME / POLICY(ONGOING)	PROJECT COST	SOURCE OF FUNDING	AMOUNT APPROPRIATED	AMOUNT RELEASED		AMOUNT UTILIZED		STATUS OF PROJECT		PLANNED MILESTONE 	ACTUAL MILESTONE	ACTUAL LEVEL OF WORK % CHALLENGES(IF ANY)	REQUIRED INTERVENTION(IF ANY)
      // PROJECT COST	AMOUNT APPROPRIATED	AMOUNT RELEASED		AMOUNT UTILIZED	%		STATUS OF PROJECT		ACTUAL MILESTONE %		ACTUAL LEVEL OF WORK %		CHALLENGES (IF ANY)	REQUIRED INTERVENTION(IF ANY)	


      this.crud.postData(query, { subData: JSON.stringify(data), mdaId: state.mda.id })
        .subscribe(res => {
          if (!res.errors) {
            // this.activeModal.hide()
            resolve('perfect')
            this.toastr.success('Upload Submitted Successfully', 'Subs')
          } else {
            // this.activeModal.hide()
            reject('oops')

            this.toastr.error(' Upload unsuccessful')
          }
        })
    });
  }
  setInfo(state) {
    this.uploadData.next(state);

  }
  getInfo(): Observable<object> {

    return this.uploadData.asObservable()
  }
}


