import { Component, Input, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, FormGroupName, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import gql from 'graphql-tag';
import { BsModalRef } from 'ngx-bootstrap';
import { ToastrService } from 'ngx-toastr';
import { Subject } from 'rxjs';
import { CrudService } from 'src/app/service/crud.service';

@Component({
  selector: 'app-add-sub-initiative',
  templateUrl: './add-sub-initiative.component.html',
  styleUrls: ['./add-sub-initiative.component.scss']
})
export class AddSubInitiativeComponent implements OnInit {
  @Input() data;
  form: FormGroup;
  submitted = false;
  onClose: any;

  constructor(private toastr: ToastrService, private crud: CrudService, public activeModal: BsModalRef, private fb: FormBuilder) {
  }

  ngOnInit() {
    this.initializeForm();
    console.log(this.data);
    this.onClose = new Subject();
  }

  submit() {
    let data = []
    if (this.data.si) {
      data = JSON.parse(this.data.si);
    }
    this.form.value.tag = this.data.tag + 'SI' + data.length;
    console.log(this.form.value);

    data.push(this.form.value);
    this.submitted = true;

    if (this.form.invalid) {
      return;
    }

    const query = gql`
      mutation createSubInitiative($id: ID!, $si: String!){
        createSubInitiative(id: $id, si: $si) {
          id
        }
      }
    `;
    this.crud.postData(query, { id: this.data.id, si: JSON.stringify(data) }).subscribe((result) => {
      // if (!result.data) {
      //   // this.error = result.errors[0].message;

      // } else {
      console.log(result);

      this.toastr.success('Sub-initiative created successful', 'Success');
      this.onClose.next(false);
      this.activeModal.hide();
      // }
    }, (error) => {
      this.toastr.error('Something went wrong, kindly try again', 'Unsuccessful');
    });
  }

  initializeForm() {
    this.form = this.fb.group({
      name: ['', Validators.required],
      type: ['', Validators.required],
      outputName: ['', Validators.required],
      outputBaseline: ['', Validators.required],
      outputActual: ['', Validators.required],
      outputYear1: ['', Validators.required],
      outputYear2: ['', Validators.required],
      outputYear3: ['', Validators.required],
      outputYear4: ['', Validators.required],

    });
  }

  get formData() {
    return {
      name: this.form.get('name'),
      type: this.form.get('type'),
      outputName: this.form.get('outputName'),
      outputBaseline: this.form.get('outputBaseline'),
      outputActual: this.form.get('outputActual'),
      outputYear1: this.form.get('outputYear1'),
      outputYear2: this.form.get('outputYear2'),
      outputYear3: this.form.get('outputYear3'),
      outputYear4: this.form.get('outputYear4'),

    };
  }
}
