  createContract() {
    this.submit = true;
    //console.log('submitting');


    let query = gql`
      mutation createContract(
 
        $title: String!,
        $budget: ID!,
        $fecId: String!,
        $contractorId: ID!,
        $mdaId: ID!,
        ){
          createContract(
            title: $title,
        budget: $budget,
        fecId: $fecId,
        mdaId: $mdaId,
       contractorId: $contractorId,
          ) {
            id
        }
        }
      `;
    this.crud.postData(query, this.contractData).subscribe((result) => {
      if (!result.data['createContract']) {
        // this.error = result.errors[0].message;

      } else {
        this.toastr.success('Contract added successfully', 'Success')
        // this.onClose.next(false);
        this.modalRef.hide();
      }
    }, (error) => {
      this.toastr.error('Something went wrong, kindly try again', 'Unsuccessful')
    });
  }

  onSubmitContractor() {
    this.submitted = true;

    // stop here if form is invalid
    if (this.contractorRegistrationForm.invalid) {
      return;
    }

    // display form values on success
    this.createContractor();
  }
  createContractor() {
    // this.submit = true;


    const query = gql`
      mutation createContractor(
        $name: String!,
        $address: String!,
        $state: String!,
        $cacNo: String!,
        ){
          createContractor(
            name: $name,
        address: $address,
       state: $state,
        cacNo: $cacNo
          ) {
            id
        }
        }
      `;
    this.crud.postData(query, this.postData).subscribe((result) => {
      if (!result.data['createContractor']) {
        // this.error = result.errors[0].message;

      } else {
        this.toastr.success("Contractor added successfully", "Success")
        // this.onClose.next(false);
        this.modalRef.hide()
      }
    }, (error) => {
      this.toastr.error("Something went wrong, kindly try again", "Unsuccessful")
    });
  }

