  getBudget() {
    const data = gql`
    query budgetByYear($year: String!) {
      budgetByYear(year: $year) {
          id
          budget_line_item
          mda{
            id
          }
        }
      }
    `;
    this.crud.pullData(data, { year: '2021' })
      .subscribe((result: any) => {
        this.budgets = result.data.budgetByYear;
      });
  }

  assignCopy() {
    this.filteredItems = this.contracts;
  }

  setFilteredItems(search) {
    //console.log(search);

    if (search == '') this.assignCopy();
    this.filteredItems = this.filteredItems.filter(item => {
      let name = item.title.toLowerCase();
      return name.indexOf(search.toLowerCase()) > -1;

    });

  }

  clearFilters() {
    this.assignCopy();
    this.searchControl.setValue('');
    // this.stateFilter = '';
    this.mdaFilter = '';
  }
  filterByMda(mda) {


    this.mdaFilter = mda.shortcode;
    if (mda == '') this.assignCopy();
    this.filteredItems = this.filteredItems.filter(item => {
      return item.mda.shortcode == mda.shortcode;
      // //console.log(item.state, state.name);

    });

  }
