import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import gql from 'graphql-tag';
import { IDropdownSettings } from 'ng-multiselect-dropdown';
import { BsModalRef } from 'ngx-bootstrap/modal';
import { ToastrService } from 'ngx-toastr';
import { Subject } from 'rxjs';
import { CrudService } from 'src/app/service/crud.service';
declare var $: any;

@Component({
  selector: 'app-add-priority-mda',
  templateUrl: './add-priority-mda.component.html',
  styleUrls: ['./add-priority-mda.component.scss']
})
export class AddPriorityMdaComponent implements OnInit {
  data: any;
  postData: any = { states: [] };
  title;
  userObj: any;
  onClose: any;
  mdaForm: FormGroup;
  ministries: any;
  selectedItems = [];
  dropdownSettings: IDropdownSettings = {};
  constructor(private fb: FormBuilder, private toastr: ToastrService, public activeModal: BsModalRef, private router: Router, private crudService: CrudService) {
  }

  ngOnInit() {
    this.title = this.data.title;
    this.onClose = new Subject();
    this.getAllMda()
    this.mdaForm = this.fb.group({
      mdas: ['', [Validators.required]]
    })
    $(document).ready(function () {
      $('.js-example-basic-multiple').select2();

    });

    this.dropdownSettings = {
      singleSelection: false,
      idField: 'id',
      textField: 'name',
      selectAllText: 'Select All',
      unSelectAllText: 'UnSelect All',
      itemsShowLimit: 3,
      allowSearchFilter: true,
      enableCheckAll:false
    };
  }

  onItemSelect(item: any) {
    this.mdaForm.value.mdas = []
    this.selectedItems.forEach(e => {
      this.mdaForm.value.mdas.push(e.id)
    });

    //console.log(this.mdaForm.value.mdas);
    
  }
  
  onItemDeSelect(item:any){
    this.mdaForm.value.mdas = []
    this.selectedItems.forEach(e => {
      this.mdaForm.value.mdas.push(e.id)
    });
  }

  get mdasFormData() {
    return {
      mdas: this.mdaForm.get('mdas')
    }
  }

  create() {
    let query = gql`
      mutation selectContributingMdas($prorityId: ID, $mdas:String!) {
        selectContributingMdas(prorityId: $prorityId, mdas:$mdas) {
          id
        }
      }
    `
    let itemObj = {
      mdas: JSON.stringify(this.mdaForm.value.mdas),
      prorityId: this.data.id
    }
    
    this.crudService.postData(query, itemObj)
      .subscribe(
        (res: any) => {

          this.toastr.success("Priority Update successful", "Success")
          this.activeModal.hide()
          this.onClose.next(false);
        },
        (error) => {
          this.toastr.error("Priority update faled, please try again", "Failed")
        });
  }

  getAllMda() {
    let data = gql`
      {
        mdas{
          id
          name
          shortcode
        }
    
      }
    `
    this.crudService.pullData(data, {})
      .subscribe((result: any) => {
        this.ministries = result.data.mdas
      });
  }

}
