import { Component, OnInit } from '@angular/core';
import gql from 'graphql-tag';
import { CrudService } from 'src/app/service/crud.service';

@Component({
  selector: 'app-notifications',
  templateUrl: './notifications.component.html',
  styleUrls: ['./notifications.component.scss']
})
export class NotificationsComponent implements OnInit {
  customClass = 'customClass';
  user: any;
  notifications: any;

  constructor(private crud:CrudService) { }

  ngOnInit() {
    this.getUserId()
  }

  getUserId(){
    this.user = JSON.parse(localStorage.getItem('user'))
    //console.log(this.user);
    
    this.getNotifications(this.user.mda.id)
  }

  getNotifications(id){
    let query = gql`
      query notificationsByMda($mdaId: ID!){
        notificationsByMda(mdaId: $mdaId){
          id
          title
          message
          createdAt
          status
        }
      }
    `
    this.crud.pullData(query, {mdaId: id})
      .subscribe((res:any)=>{
        this.notifications = res.data.notificationsByMda;
        //console.log(this.notifications);
        
      })
  }

  changeStatus(item){
    let query=gql`
      query changeNotificationStatus($id: ID!){
        changeNotificationStatus(id: $id){
          id
        }
      }
    `
    this.crud.pullData(query, {id: item.id})
      .subscribe((res:any)=>{
        this.getNotifications(this.user.mda.id);
      })
  }

  // getDate(data){
  //   let date = new Date(data);
    
  //   //console.log(date);
  // }
}
