import { Component, OnInit } from '@angular/core';
import { CrudService } from '../../../service/crud.service';
import gql from 'graphql-tag';
import { Router } from '@angular/router';

@Component({
  selector: 'app-view-projects',
  templateUrl: './view-projects.component.html',
  styleUrls: ['./view-projects.component.scss']
})
export class ViewProjectsComponent implements OnInit {
  deliverableId: any;
  projects: any = [];
  p: any
  role: any;
  pageSize: any = 10
  constructor(private crud: CrudService, private router: Router) {
    this.role = JSON.parse(localStorage.getItem('role'));

  }

  ngOnInit(): void {
    this.pullProject()
  }
  pullProject() {
    let data = gql`
  query projectsByDeliverables($deliverableId: ID!) {
    projectsByDeliverables(deliverableId:$deliverableId) {
      id
      title
      state
      budget
    status
  mda{
    shortcode
  }
    # createdAt
      }
    }
  `
    this.crud.pullData(data, { deliverableId: this.deliverableId })
      .subscribe((result: any) => {
        this.projects = result.data['projectsByDeliverables'];
      });
  }
  openProjectReports(item) {
    this.router.navigate(['/home/project/project-report-log'], {
      state: {
        log: JSON.stringify(item)
      }
    })

  }
}
