import { ChangeDetectionStrategy, Component, OnInit } from '@angular/core';
import gql from 'graphql-tag';
import { CrudService } from 'src/app/service/crud.service';

@Component({
  selector: 'app-fiscal-management',
  changeDetection: ChangeDetectionStrategy.OnPush,
  templateUrl: './fiscal-management.component.html',
  styleUrls: ['./fiscal-management.component.scss']
})
export class FiscalManagementComponent implements OnInit {

  active = 'budget';
  budget: any;
  report: any;
  activeYear: string;
  constructor(private crud: CrudService) {
    this.activeYear = localStorage.getItem('activeYear');
  }

  ngOnInit(): void {
    // this.getBudget();
    // this.getReport();
  }

  getBudget(){
    const data = gql`
    query budgetByYear($year: String!) {
      budgetByYear(year: $year) {
          budget_line_item
          budget_code
          amount
          year
          mda{
            id
            name
            shortcode
          }
        }
      }
    `;
    this.crud.pullData(data, { year: this.activeYear + '' })
      .subscribe((result: any) => {
        this.budget = result.data.budgetByYear;
      });
  }

  getReport(){
    const data = gql`
    query releaseByYear($year: String!) {
      releaseByYear(year: $year) {
          quarter
          title
          released
          utilized
          budget
          year
          mda{
            id
            name
            shortcode
          }
        }
      }
    `;
    this.crud.pullData(data, {year: this.activeYear + '' })
      .subscribe((result: any) => {
        this.report = result.data.releaseByYear;
      });
  }

  uploadBudget(){
    this.active = 'budget';
  }

  uploadRelease(){
    this.active = 'releases';

  }
}
