import { Component, OnInit } from '@angular/core';
import gql from 'graphql-tag';
import { CrudService } from '../../../service/crud.service';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-view-sub-initiative',
  templateUrl: './view-sub-initiative.component.html',
  styleUrls: ['./view-sub-initiative.component.scss']
})
export class ViewSubInitiativeComponent implements OnInit {
  data: any;
  si: any;
  constructor(private crud: CrudService, private toastr: ToastrService) {
  }

  ngOnInit(): void {
    console.log(this.data);

    if (this.data.si)
      this.si = JSON.parse(this.data.si);
    console.log(this.si);
  }
  delete(val) {
    let index = this.si.indexOf(val);
    console.log(index);
    let r = confirm("Are you want to delete this sub!");
    if (r == true) {



      this.si.splice(index, 1);
      console.log(this.si);


      const query = gql`
      mutation createSubInitiative($id: ID!, $si: String!){
        createSubInitiative(id: $id, si: $si) {
          id
        }
      }
    `;
      this.crud.postData(query, { id: this.data.id, si: JSON.stringify(this.si) }).subscribe((result) => {
        // if (!result.data) {
        //   // this.error = result.errors[0].message;

        // } else {
        console.log(result);

        this.toastr.success('Sub-initiative deleted successful', 'Success');
        // this.onClose.next(false);
        // this.activeModal.hide();
        // }
      }, (error) => {
        this.toastr.error('Something went wrong, kindly try again', 'Unsuccessful');
      });

    } else {
      return
    }
  }
  edit() {

  }
}
