import { Component, OnInit } from '@angular/core';
import { Title } from '@angular/platform-browser';
import { Router, NavigationEnd, ActivatedRoute } from '@angular/router';
import gql from 'graphql-tag';
import { filter, map } from 'rxjs/operators';
import { CrudService } from '../service/crud.service';
import { ToastrService } from 'ngx-toastr';
import { FunctionsService } from '../service/functions.service';
declare const $: any;

@Component({
  selector: 'app-dashboard-container',
  templateUrl: './dashboard-container.component.html',
  styleUrls: ['./dashboard-container.component.scss']
})
export class DashboardContainerComponent implements OnInit {
  title = '';
  year = [];
  calendar = '';
  permission: any;



  componentName
  setactive = true;
  mdasidebar = false;
  lightmode = false;
  fiscal = false;

  menu = [
    {
      title: "Dashboard",
      url: "/dashboard/home",
      // icon: faTachometerAlt
    },
    {
      title: "Mandate",
      url: "/dashboard/mandates",
      // icon: faShoppingCart
    },
    {
      title: "Budget",
      url: "/dashboard/user-management",
      // icon: faReceipt
    },

  ];
  current: any;
  user: any;
  userAvailable: boolean;
  notifications: any;
  counter: any;
  role: string;

  search: any = {};
  uploading: any;
  uploadState: any;
  constructor(private route: ActivatedRoute, private toastr: ToastrService, private functions: FunctionsService, private router: Router, private crud: CrudService) {
    if (localStorage.getItem('mode')) {
      this.lightmode = JSON.parse(localStorage.getItem('mode'));
    }
    this.role = localStorage.getItem('role');

    this.switchmode();
    this.current = (route.snapshot['_routerState'].url);
    this.user = JSON.parse(localStorage.getItem('user'));
    if (this.user != null) {
      this.userAvailable = true;

    }
    this.getTitle();
    this.getUserId();
    this.permission = JSON.parse(this.user.role.permissions);
    console.log(this.permission);
    this.functions.getInfo().subscribe(value => {
      // do something with this value
      this.uploadState = value;


      console.log('Info got changed to: ', value);
    });
  }

  ngOnInit() {
    $(document).ready(function () {
      $('#sidebarCollapse').on('click', function () {
        $('#sidebar').toggleClass('active');
      });
    });
  }

  getTitle() {
    const appTitle = '';
    this.router
      .events.pipe(
        filter(event => event instanceof NavigationEnd),
        map(() => {
          const child = this.route.firstChild;
          if (child.snapshot.data['title']) {
            return child.snapshot.data['title'];
          }
          return appTitle;
        })
      ).subscribe((ttl: string) => {
        // this.titleService.setTitle(ttl);
        this.title = ttl;
      });
    $(document).ready(function () {
      $('#sidebarCollapse').on('click', function () {
        $('#sidebar').toggleClass('active');
      });
    });
  }
  gotoProfile() {
    this.router.navigate(['/home/settings/system'])
  }

  gotoDashboard() {
    this.router.navigate(['/home/dashboard'])
  }

  gotoResultTree() {
    this.router.navigate(['/home/result-tree'])
  }

  gotoProject() {
    this.router.navigate(['/home/projects'])
  }

  gotoMessage() {
    this.router.navigate(['/home/message/log'])
  }

  gotSettings() {
    this.router.navigate(['/home/settings/system'])
  }
  
  gotDeliverable() {
    this.router.navigate(['/home/deliverable-management'])
  }

  openFiscalDropDown() {
    if (!this.setactive) {
      this.setactive = !this.setactive
    };
    this.fiscal = !this.fiscal;
  }

  activateSidebar() {
    this.setactive = !this.setactive
    if (!this.setactive) {
      this.fiscal = false
    };

  }

  switchmode() {
    if (this.lightmode) {
      localStorage.setItem('mode', 'true')
      document.documentElement.setAttribute('data-theme', 'light')
      return
    }

    document.documentElement.setAttribute('data-theme', 'dark')
    localStorage.setItem('mode', 'false');
  }

  logout() {
    this.user = null;
    localStorage.removeItem('user');
    localStorage.removeItem('token');
    localStorage.removeItem('permissions');


    this.router.navigate(['/login'])
  }

  getUserId() {
    this.user = JSON.parse(localStorage.getItem('user'))
    // this.getNotifications(this.user.mda.id)
  }

  getNotifications(id) {
    let query = gql`
      query notificationsByMda($mdaId: ID!){
        notificationsByMda(mdaId: $mdaId){
          id
          status
        }
      }
    `
    this.crud.pullData(query, { mdaId: id })
      .subscribe((res: any) => {
        this.notifications = res.data.notificationsByMda;
        this.counter = this.notifications.filter((e) => e.status == 'unread')
        //console.log(this.counter);

      })
  }

  gotoNotification() {
    this.router.navigate(['/home/notifications/log'])
  }

  onSearch() {

    this.router.navigate(['/home/search/' + this.search.query])

  }
}
