import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { BsModalRef } from 'ngx-bootstrap/modal';
import { ToastrService } from 'ngx-toastr';
import { CrudService } from 'src/app/service/crud.service';
import gql from 'graphql-tag';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Subject } from 'rxjs';
declare var $: any;

@Component({
  selector: 'app-edit-mandate',
  templateUrl: './edit-mandate.component.html',
  styleUrls: ['./edit-mandate.component.scss']
})
export class EditMandateComponent implements OnInit {
  data: any;
  postData: any = { states: [] };
  submit: any;
  sh: any;
  states: any;
  title;
  userObj: any;
  ministries: any;
  mandateForm: FormGroup;
  onClose: any;
  change = false;
  currentMinistry: any;
  id: any;
  constructor(private fb: FormBuilder, private toastr: ToastrService, public activeModal: BsModalRef, private router: Router, private crudService: CrudService) {

  }

  ngOnInit() {
    this.mandateForm = this.fb.group({
      title: [this.data.title, [Validators.required]],
      mda: [this.data.mdaId, [Validators.required]],
    })

    this.title = this.data.title;
    this.onClose = new Subject();
    $(document).ready(function () {
      $('.js-example-basic-multiple').select2();

    });
    this.getAllMda()

  }

  get mandateFormData() {
    return this.mandateForm.controls;
  }

  create() {
    this.mandateForm.value.id = this.data.id;
    this.submit = true;
    let query = gql`
    mutation editMandate($id: ID!, $title:String!, $mda:ID!) {
      editMandate(id: $id, title:$title, mda:$mda) {
        id
        title
       
      }
      }
    `
    // //console.log(this.mandateForm.value);

    this.crudService.postData(query, this.mandateForm.value)
      .subscribe(
        (res: any) => {
          this.toastr.success("Mandate successful edited", "Success")
          this.onClose.next(false);
          this.activeModal.hide()
        },
        (error) => {
          this.toastr.error("Mandate edited unsuccessly, kindly try again", "Unsuccessful")
        });
  }


  getAllMda() {
    let data = gql`
    {
      mdas{
        id
        name
        shortcode
      }
      
    }
    `
    this.crudService.pullData(data, {})
      .subscribe((result: any) => {
        this.ministries = result.data.mdas

        this.currentMinistry = this.ministries.filter(e => e.id === this.data.mdaId);

      });
  }
}  