import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import gql from 'graphql-tag';
import { ToastrService } from 'ngx-toastr';
import { CrudService } from '../service/crud.service';
import { IDropdownSettings } from 'ng-multiselect-dropdown';
import { Router } from '@angular/router';
declare var $: any
@Component({
  selector: 'app-user-settings',
  templateUrl: './user-settings.component.html',
  styleUrls: ['./user-settings.component.scss']
})
export class UserSettingsComponent implements OnInit {
  userForm: FormGroup;
  editUserForm: FormGroup;
  roles;
  permissions;
  ministries;
  users: any;
  editData: any;
  calendar = ''
  years;
  rolePermission: any = {};
  user;
  roleForm: FormGroup;
  singleRole: any;
  temp: any;
  pageSize = 10;
  password = false;
  username = false;
  selectedItems = [];
  dropdownSettings: IDropdownSettings = {};
  permission: any;
  mdaform: FormGroup;
  activeYear
  yearform: FormGroup;
  constructor(private fb: FormBuilder, private crud: CrudService, private router: Router, private toastr: ToastrService) {
    this.roles = [{ name: 'Admin', permissions: [] }, { name: 'Stakeholder', permissions: [] }, { name: 'User', permissions: [] }, { name: 'Project Manager', permissions: [] }, { name: 'Reporter', permissions: [] }]
    this.permissions = ['rf', 'fm', 'pm', 'rep', 'db', 'st']

    this.initializeEditData()
    this.getUserId()
    if (!localStorage.getItem('years')) {
      this.years = ['2019', '2020', '2021']
      localStorage.setItem('years', JSON.stringify(this.years))
    } else {
      this.years = JSON.parse(localStorage.getItem('years'));
    }
    this.years.reverse()

    const currentYear = new Date().getFullYear();
    const activeYear = localStorage.getItem('activeYear');
    if (activeYear) {
      this.activeYear = activeYear;
    } else {
      this.activeYear = currentYear;
    }
  }

  ngOnInit() {
    this.createUser();
    this.getAllUsers();
    this.getAllRoles();
    this.getAllMda()
    this.initializeRoleForm()
    this.updateUser();
    this.initializemdaform();
    this.initializeyearform();

    this.permission = JSON.parse(this.user.role.permissions);

    $(document).ready(function () {
      $('.js-example-basic-multiple').select2({
        placeholder: 'Please Select contributing Ministries',
        multiple: true,
        width: '100%',
        search: true,
        matcher: this.matchCustom
      });

    });

    this.dropdownSettings = {
      singleSelection: false,
      idField: 'id',
      textField: 'name',
      selectAllText: 'Select All',
      unSelectAllText: 'UnSelect All',
      itemsShowLimit: 3,
      allowSearchFilter: true,
      enableCheckAll: false
    };
  }

  onItemSelect(item: any) {
    this.userForm.value.ministry = []
    this.selectedItems.forEach(e => {
      this.userForm.value.ministry.push(e.id)
    });

    //console.log(this.userForm.value.ministry);

  }


  onItemDeSelect(item: any) {
    this.userForm.value.ministry = [];
    this.selectedItems.forEach(e => {
      this.userForm.value.ministry.push(e.id);
    });
  }

  initializemdaform() {
    this.mdaform = this.fb.group({
      mdaname: ['', Validators.required],
      mdashortcode: ['', Validators.required]
    })
  }

  initializeyearform() {
    this.yearform = this.fb.group({
      year: ['', [Validators.minLength(4), Validators.maxLength(4), Validators.required]]
    })
  }

  matchCustom(params, data) {
    if ($.trim(params.term) === '') {
      return data;
    }
    if (typeof data.text === 'undefined') {
      return null;
    }

    if (data.text.indexOf(params.term) > -1) {
      var modifiedData = $.extend({}, data, true);
      modifiedData.text += ' (matched)';

      return modifiedData;
    }
    return null;
  }
  createUser() {
    this.userForm = this.fb.group({
      "name": ['', [Validators.required]],
      "role": ['', [Validators.required]],
      "ministry": ['', [Validators.required]],
      "email": ['', [Validators.required, Validators.email]],
      "phone": ['', [Validators.required, Validators.maxLength(11), Validators.minLength(11)]]
    });
  }

  updateUser() {
    this.editUserForm = this.fb.group({
      username: this.user.name,
      password: '',
    });
  }

  initializeRoleForm() {
    this.roleForm = this.fb.group({
      name: ['', [Validators.required]],
      permissions: ['', [Validators.required]],
    });
  }

  get editUserFormData() {
    return {
      username: this.editUserForm.get('username'),
      password: this.editUserForm.get('password'),
    };
  }

  get roleFormData() {
    return {
      name: this.roleForm.get('name'),
      permissions: this.roleForm.get('permissions'),
    };
  }

  get userFormData() {
    return {
      name: this.userForm.get('name'),
      role: this.userForm.get('role'),
      ministry: this.userForm.get('ministry'),
      email: this.userForm.get('email'),
      phone: this.userForm.get('phone')
    };
  }

  submitUserForm() {
    //console.log(this.userForm.value.ministry);
    if (this.editData.id) {
      this.updateUserForm();
      return;
    }

    const query = gql`
    mutation createUser($userInput: userInputType!) {
      createUser(userInput: $userInput) {
        id
      }
    }
    `;
    const itemObj = {
      name: this.userForm.value.name,
      mdaId: JSON.stringify(this.userForm.value.ministry),
      password: '12345678',
      email: this.userForm.value.email,
      phone: this.userForm.value.phone,
      roleId: this.userForm.value.role
    };

    this.crud.postData(query, { userInput: itemObj })
      .subscribe(
        (res: any) => {
          $('#createUserModal').modal('hide');
          this.toastr.success('New user was created successfully', 'Success');
          this.initializeEditData();
          this.getAllUsers();
          this.userForm.reset();
        },
        (error) => {
          this.toastr.error('Failed to create new user, kindly try again', 'Unsuccessful');
        });
  }

  getAllUsers() {
    const data = gql`
      {
      users{
        id
        name
        email
        role{
          id
          name
          permissions
        }
        mda{
          id
          name
          shortcode
        }
        phone
      }
    }
  `;
    this.crud.pullData(data, {})
      .subscribe((result: any) => {
        this.users = result.data.users;
        this.temp = result.data.users;

        setInterval(() => {
          $('.users').DataTable();
        }, 1000);
      });
  }

  getAllRoles() {
    const data = gql`
      {
        roles{
          id
          name
          permissions
        }
      }
    `;
    this.crud.pullData(data, {})
      .subscribe((result: any) => {
        let roles = [];
        result.data.roles.forEach(e => {
          e.permissions = JSON.parse(e.permissions);
          roles.push(e);
        });
        this.roles = roles;

        setInterval(() => {
          $('.roles').DataTable();
        }, 1000);
      });
  }

  updateUserForm() {
    //console.log(this.userForm.value.ministry);
    let query = gql`
    mutation editUser($userInput: userInputType!) {
      editUser(userInput: $userInput) {
        id
      }
    }
    `;
    const itemObj = {
      id: this.editData.id,
      name: this.userForm.value.name,
      mdaId: JSON.stringify(this.userForm.value.ministry),
      password: '12345678',
      email: this.userForm.value.email,
      phone: this.userForm.value.phone,
      roleId: this.userForm.value.role
    };
    this.crud.postData(query, { userInput: itemObj })
      .subscribe(
        (res: any) => {
          $('#createUserModal').modal('hide');
          this.toastr.success('User data is successfully updated', 'Success');
          this.initializeEditData();
          this.getAllUsers();
          this.userForm.reset();
          // this.openNewUser = false
        },
        (error) => {
          this.toastr.error('Failed to update user data, kindly try again', 'Unsuccessful');
        });
  }

  deleteUser(id) {
    const data = gql`
    mutation deleteUser($id: ID!){
      deleteUser(id: $id) {
        id
      }
    }
    `;
    this.crud.pullData(data, { id: this.user.id })
      .subscribe(
        (result: any) => {
          this.toastr.error('Oops, the delete operation was unsuccessful, please try again', 'Unsuccessful');

        },
        (error: any) => {
          this.getAllUsers();
          this.toastr.success('The user was successfully deleted', 'Success');
          $('#deleteUserModal').modal('hide');
        }
      );
  }

  getAllMda() {
    const data = gql`
    {
      mdas{
        id
        name
        shortcode
      }
    }
    `;
    this.crud.pullData(data, {})
      .subscribe((result: any) => {
        this.ministries = result.data.mdas;
      });
  }

  initializeEditData() {
    this.editData = {
      name: '',
      role: { id: '' },
      mda: { id: '' },
      email: '',
      phone: ''
    };
  }

  editUser(i) {
    this.editData = i;

    $('#createUserModal').modal('show')
  }

  updateUserProfile() {
    let query
    let itemObj

    if (this.editUserForm.value.password) {
      query = gql`
    mutation editUser($userInput: userInputType!) {
      editUser(userInput: $userInput) {
        id
        name
        access
        email
        password
        phone
        role{
          id
          name
          permissions
        }
        mda{
          id
          name
          shortcode
        }
      }
    }
    `

      itemObj = {
        id: this.user.id,
        name: this.editUserForm.value.username,
        password: this.editUserForm.value.password,
        mdaId: this.user.mda.id,
        email: this.user.email,
        roleId: this.user.role.id
      };
    } else {
      query = gql`
    mutation editUser($userInput: userInputType!) {
      editUser(userInput: $userInput) {
        id
        name
        access
        email
        password
        phone
        role{
          id
          name
          permissions
        }
        mda{
          id
          name
          shortcode
        }
      }
    }
    `
      itemObj = {
        id: this.user.id,
        name: this.editUserForm.value.username,
        mdaId: this.user.mda.id,
        email: this.user.email,
        roleId: this.user.role.id
      }
    }
    //console.log(itemObj);

    this.crud.postData(query, { userInput: itemObj })
      .subscribe(
        (res: any) => {
          $('#createUserModal').modal('hide')
          this.toastr.success("User data is successfully updated", "Success")
          // this.openNewUser = false
          localStorage.setItem('user', JSON.stringify(res.data.editUser));
          this.getUserId()
        },
        (error) => {
          this.toastr.error("Failed to update user data, kindly try again", "Unsuccessful")
        });
  }

  openRoleModal(role) {
    this.rolePermission = role;
    $('#assignPermissionModal').modal('show');
  }

  updateRole() {
    let query = gql`
    mutation editRoles($id: ID!, $name: String!, $permissions: String!) {
      editRoles(id: $id, name: $name, permissions: $permissions) {
        id
      }
    }
    `;
    let itemObj = {
      id: this.rolePermission.id,
      name: this.rolePermission.name,
      permissions: JSON.stringify(this.rolePermission.permissions),
    }
    this.crud.postData(query, itemObj)
      .subscribe(
        (res: any) => {
          $('#assignPermissionModal').modal('hide')
          this.toastr.success("Role's data is successfully updated", "Success")
          this.initializeEditData()
          this.getAllRoles()
          this.rolePermission = {}
          // this.openNewUser = false
        },
        (error) => {
          this.toastr.error("Failed to update Role data, kindly try again", "Unsuccessful")
        });
  }

  createRole() {
    let query = gql`
    mutation createRoles($name: String!, $permissions: String!) {
      createRoles(name: $name, permissions: $permissions) {
        id
      }
    }
    `
    let itemObj = {
      name: this.roleForm.value.name,
      permissions: JSON.stringify(this.roleForm.value.permissions),
    }
    this.crud.postData(query, itemObj)
      .subscribe(
        (res: any) => {
          $('#createRoleModal').modal('hide')
          this.toastr.success("Role is successfully created", "Success")
          this.initializeEditData()
          this.getAllRoles()
          // this.openNewUser = false
        },
        (error) => {
          this.toastr.error("Failed to create Role, kindly try again", "Unsuccessful")
        });
  }

  deleteRole() {
    let data = gql`

    query deleteRole($id: ID!){
      deleteRole(id: $id) {
        id
      }

    }
    `
    this.crud.pullData(data, { id: this.singleRole })
      .subscribe(
        (result: any) => {
          this.getAllRoles();
          this.toastr.success("The operation was successful", "Success")
          $('#deleteRoleModal').modal('hide')
        },
        (error: any) => {
          this.toastr.error("Oops, the delete operation was unsuccessful, please try again", "Unsuccessful")
        }
      );
  }

  abbreviatePermissions(item) {
    let aText = '';
    if (item === 'dashboard') {
      aText = 'db';
    } else if (item === 'settings') {
      aText = 'st';
    } else {
      aText = item;
    };
    return aText
  }

  updateFilter(event) {
    let val = event.target.value;

    // filter our data

    const temp = this.temp.filter((d: any) => {
      let name: any = d.name
      let email: any = d.email
      let role = d.role.name
      let phone = d.phone || '0'

      // let ministry = d.mda.name
      // let phone = d.mda.name
      return name.toLowerCase().indexOf(val) !== -1 || name.indexOf(val) !== -1 || !val || email.toLowerCase().indexOf(val) !== -1 || email.indexOf(val) !== -1 || role.toLowerCase().indexOf(val) !== -1 || role.indexOf(val) !== -1 || phone.toLowerCase().indexOf(val) !== -1;
    });

    this.users = temp;
  }

  changePageSize(e) {
    if (e.target.value == 'all') {
      this.pageSize = this.users.length
    } else {
      this.pageSize = parseInt(e.target.value)
    }

  }

  getUserId() {
    this.user = JSON.parse(localStorage.getItem('user'))
    //console.log(this.user);

  }

  selectMinistry(e) {
    //console.log(e);

  }

  gotoPrioritySettings() {
    this.router.navigate(['home/settings/priorities']);
  }

  requestReports() {
    const data = gql`
    mutation {
      requestReport {
        status
      }
    }
    `;
    this.crud.postData(data, { id: this.user.id })
      .subscribe(
        (result: any) => {
          if (result.data['requestReport'].status == 'Success')

            this.toastr.success('Reports successfully requested', 'Success');
          else
            this.toastr.error('Oops, Something went wrong, please try again', 'Unsuccessful');

        },
        (error: any) => {
          this.toastr.error('Oops, Something went wrong, please try again', 'Unsuccessful');

        }
      );
  }

  createmda() {

  }

  setCurrentYear() {
    let query = gql`
    mutation editUser($userInput: userInputType!) {
      editUser(userInput: $userInput) {
        id
        name
        access
        email
        password
        phone
        role{
          id
          name
          permissions
        }
        mda{
          id
          name
          shortcode
        }
      }
    }
    `

    //console.log(itemObj);

    this.crud.postData(query, { userInput: { id: this.user.id, name: this.user.name, email: this.user.email, activeYear: this.activeYear } })
      .subscribe(
        (res: any) => {
          localStorage.setItem('activeYear', this.activeYear);
          this.toastr.success("Focus year changed to " + this.activeYear + " successfully", "Success");
        },
        (error) => {
          this.toastr.error("Failed to update user data, kindly try again", "Unsuccessful")
        });

  }

  addyear() {
    console.log(this.yearform.status);
    const year = this.yearform.value.year.toString()
    if (year.length !== 4) {
      this.toastr.error('Value must be 4 digit')
      return
    }

    if (this.years.includes(year)) {
      this.toastr.warning('Entered value already exit')
      return
    }

    let years = JSON.parse(localStorage.getItem('years'))
    years.push(year);

    localStorage.setItem('years', JSON.stringify(years));
    this.years = years;
    this.years.reverse();
  }
}
