import { Component, OnInit } from '@angular/core';
import { BsModalService } from 'ngx-bootstrap';
import { ComposeMessageComponent } from './modal/compose-message/compose-message.component';

@Component({
  selector: 'app-message-log',
  templateUrl: './message-log.component.html',
  styleUrls: ['./message-log.component.scss']
})
export class MessageLogComponent implements OnInit {

  constructor(private modalService:BsModalService) { }

  ngOnInit() {
  }

  Void(){}

  getFirstCharater(data){
    return data[0];
  }

  openComposeMessage(){
    const modalRef = this.modalService.show(ComposeMessageComponent, { class: ''});
    modalRef.content.onClose.subscribe(result => {

    })
  }
}
