import { ToastrService } from 'ngx-toastr';
import { Component, OnInit, Input } from '@angular/core';
import { BsModalRef } from 'ngx-bootstrap/modal';
import * as XLSX from 'xlsx';
import { CrudService } from '../service/crud.service';
import gql from 'graphql-tag';

@Component({
  selector: 'app-desk-review',
  templateUrl: './desk-review.component.html',
  styleUrls: ['./desk-review.component.scss']
})
export class DeskReviewComponent implements OnInit {
  data: any;
  mda: any;
  report: any;
  type: any;
  rows: any =
    [
      ["Project Unique ID", "ProjectName", "Budget", "Approved Cost", "Cummulative Release", "Cummullative Utilization", "status", "Level of Completion (%)", "Comments/Observations", "Challenges encountered (if any)", "Output Indicator", "Output value", "Longitude", "Latitude"]
    ]


  csvContent: any
  projects: any
  sp: any;
  indicator: any;
  submitted: boolean;

  filedata: any;
  thead: any;
  tbody: any;
  constructor(public activeModal: BsModalRef, private toast: ToastrService, private crud: CrudService, private toastr: ToastrService) {
    // if (JSON.parse(localStorage.getItem('projects')) != null)
    // this.projects = JSON.parse(localStorage.getItem('projects')); 
  }

  ngOnInit() {
    this.projects = this.data;
    //console.log(this.projects);

    //console.log(this.rows);
  }




  onOutputFileChange(evt: any) {
    console.log('output');

    this.tbody = []
    const target: DataTransfer = <DataTransfer>(evt.target);
    if (target.files.length !== 1) throw new Error('Cannot use multiple files');

    const reader: FileReader = new FileReader();
    reader.onload = (e: any) => {
      /* read workbook */
      const bstr: string = e.target.result;
      const wb: XLSX.WorkBook = XLSX.read(bstr, { type: 'binary' });

      /* grab first sheet */
      const wsname: string = wb.SheetNames[0];
      const wsname2: string = wb.SheetNames[1];
      const ws: XLSX.WorkSheet = wb.Sheets[wsname];
      const ws2: XLSX.WorkSheet = wb.Sheets[wsname2];

      /* save data */

      this.filedata = XLSX.utils.sheet_to_json(ws2, { header: 1 });
      this.filedata = this.filedata.slice(7)
      console.log(this.filedata);

      // this.thead = this.data[0]
      // for (let i = 0; i < this.thead.length; i++) {
      //   this.tbody.push
      // }
      this.filedata.shift();

      for (let i = 0; i < this.filedata.length; i++) {
        this.tbody.push(this.filedata[i]);
      }
      // this.submitDeskReview()
    };
    reader.readAsBinaryString(target.files[0]);
  }

  onFileChange(evt: any) {

    this.tbody = []
    const target: DataTransfer = <DataTransfer>(evt.target);
    if (target.files.length !== 1) throw new Error('Cannot use multiple files');

    const reader: FileReader = new FileReader();
    reader.onload = (e: any) => {
      /* read workbook */
      const bstr: string = e.target.result;
      const wb: XLSX.WorkBook = XLSX.read(bstr, { type: 'binary' });

      /* grab first sheet */
      const wsname: string = wb.SheetNames[0];
      const wsname2: string = wb.SheetNames[1];
      const ws: XLSX.WorkSheet = wb.Sheets[wsname];
      const ws2: XLSX.WorkSheet = wb.Sheets[wsname2];

      /* save data */

      this.filedata = XLSX.utils.sheet_to_json(ws2, { header: 1 });
      this.filedata = this.filedata.slice(7)
      console.log(this.filedata);

      // this.thead = this.data[0]
      // for (let i = 0; i < this.thead.length; i++) {
      //   this.tbody.push
      // }
      this.filedata.shift();

      for (let i = 0; i < this.filedata.length; i++) {
        this.tbody.push(this.filedata[i]);
      }
      this.submitDeskReview()
    };
    reader.readAsBinaryString(target.files[0]);
  }

  submitDeskReview() {
    setTimeout(() => {
      this.activeModal.hide()

    }, 3000);
    let query = gql`
      mutation uploadLogframe($logframeData:String!, $projectData:String!, $mdaId: ID!, $report: String){
        uploadLogframe(logframeData:$logframeData, projectData:$projectData, mdaId: $mdaId, report: $report){
          status
        }
      }
    `
    // S / N	PROJECT UNIQUE ID	FEC ID.TITLE 	APPROVAL DATE	SIGNED MINISTERIAL DELIVERABLES / PRIORITY PROJECTS	LOCATION	GPS COORDINATES	COMMENCEMENT DATE	PLANNED COMPLETION DATE	PROJECT / PROGRAMME / POLICY(ONGOING)	PROJECT COST	SOURCE OF FUNDING	AMOUNT APPROPRIATED	AMOUNT RELEASED		AMOUNT UTILIZED		STATUS OF PROJECT		PLANNED MILESTONE 	ACTUAL MILESTONE	ACTUAL LEVEL OF WORK % CHALLENGES(IF ANY)	REQUIRED INTERVENTION(IF ANY)
    // PROJECT COST	AMOUNT APPROPRIATED	AMOUNT RELEASED		AMOUNT UTILIZED	%		STATUS OF PROJECT		ACTUAL MILESTONE %		ACTUAL LEVEL OF WORK %		CHALLENGES (IF ANY)	REQUIRED INTERVENTION(IF ANY)	
    let uploadObj = [];
    let batch = Math.floor(1000 + Math.random() * 9000)
    this.tbody.forEach(el => {
      if (el[0] != null || el[2] != null) {

        uploadObj.push({
          puid: el[1],
          fecId: el[2],
          title: el[3],
          approvalDate: el[4],
          duuid: el[6],
          location: el[7],
          start_date: el[9],
          end_date: el[10],
          // longitude: el[5],
          // latitude: el[6],
          currency: el[12],
          cost: el[13],
          budget: el[13],
          variationCost: el[14],
          fundingSource: el[15],
          appropriated: el[16],
          released: el[17],
          utilized: el[18],
          status: el[19],
          planned_milestone: el[20],
          actual_milestone: el[21],
          loc: el[22],
          challenges: el[23],
          intervention: el[24],
          images: el[25],
          batch: batch

        })
      }

    });


    console.log(uploadObj);

    this.crud.postData(query, { report: this.report, logframeData: JSON.stringify(uploadObj), projectData: JSON.stringify(uploadObj), mdaId: this.mda.id })
      .subscribe(res => {
        if (!res.errors) {
          this.activeModal.hide()
          this.toastr.success('Upload Submitted Successfully', 'Desk Review')
        } else {
          // this.activeModal.hide()
          this.toastr.error(' Upload unsuccessful', 'Desk Review')
        }
      })
  }
}
