

import { Component, OnInit } from '@angular/core';
import { FormBuilder, Validators } from '@angular/forms';
import gql from 'graphql-tag';
import { Router } from '@angular/router';
import { CrudService } from '../service/crud.service';
import { ActivityService } from '../service/activity.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {
  postForm: any;
  submitted: boolean;
  error: string;
  mdas: any;

  constructor(private formBuilder: FormBuilder, private activity: ActivityService, private router: Router, private crud: CrudService) {
    this.postForm = this.formBuilder.group({
      email: ["", [Validators.required]],
      password: ["", [Validators.required]],
    });

    let token = localStorage.getItem('token');
    if (token) {
      this.router.navigate(['/admin']);
    }
  }
  get f() {
    return this.postForm.controls;
  }

  ngOnInit() {
    this.getMda();
  }

  login() {
    this.submitted = true;
    if (this.postForm.invalid) {
      return;
    }

    let query = gql`
    mutation login(
      $email: String!
      $password: String!){
        login(email: $email, password: $password) {
          token,
          user{
            id 
            activeYear
            name
            access
            email
            password
            phone
            role{
              id
              name
              permissions
            }
            mdaList
            mda{
              id
              name
              shortcode
            }
          }
        }
      }
    `;
    this.crud.postData(query, this.postForm.value).subscribe((result) => {
      if (!result.data['login']) {
        this.error = result.errors[0].message;
      }
      let access;
      localStorage.setItem('token', result.data['login'].token);
      localStorage.setItem('activeYear', result.data['login'].activeYear);

      localStorage.setItem('role', JSON.stringify(result.data['login'].user.role.name));
      localStorage.setItem('permissions', JSON.stringify(result.data['login'].user.role.permissions));
      localStorage.setItem('user', JSON.stringify(result.data['login'].user));
      if (result.data['login'].user.role == "Admin") {
        localStorage.setItem('mda', JSON.stringify(this.mdas));
      } else {
        localStorage.setItem('mda', JSON.stringify(result.data['login'].user.mda));
        localStorage.setItem('mdaList', JSON.stringify(result.data['login'].user.mdaList));
      }
      localStorage.setItem('activeYear', '' + new Date().getFullYear())
      let activityInput = {
        type: 'Admin Access',
        operation: 'login',
        userId: result.data['login'].user.id,
      }
      this.activity.record(activityInput);


      this.router.navigate(['/home'])
    })

  }

  getMda() {
    let data = gql`
    query mdas{
      mdas{
        id
        name
        shortcode
      }
    }
  `;
    this.crud.pullData(data, {})
      .subscribe(result => {
        this.mdas = result.data['mdas'];
        localStorage.setItem('mdas', JSON.stringify(this.mdas));
        localStorage.setItem('activeYear', '2021');
        // this.mandates['output'] = 0;

      },
        error => {
          // this.toastr.error("Oops, we cannot fetch your data at this time, kindly try again", "Unsuccessful")
        });
  }

}
