import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import gql from 'graphql-tag';
import { ToastrService } from 'ngx-toastr';
import { CrudService } from 'src/app/service/crud.service';
import * as XLSX from 'xlsx';
import { BsModalService } from 'ngx-bootstrap';
import { OagfReportManagerComponent } from '../oagf-report-manager/oagf-report-manager.component';
declare var $: any;

@Component({
  selector: 'app-oagf-report',
  templateUrl: './oagf-report.component.html',
  styleUrls: ['./oagf-report.component.scss']
})
export class OagfReportComponent implements OnInit {
  uploadForm: FormGroup;
  tbody: any[];
  filedata: any;
  mda: any;
  rowIndex: any;
  report: any;
  purge: any;
  selectedQuater: any;
  ministries: any;
  p: any;
  purgeReleaseByMinistry: FormGroup;
  purgeReleaseByQuarter: FormGroup;
  activeYear;
  constructor(private fb: FormBuilder, private modalService: BsModalService, private crud: CrudService, private toastr: ToastrService) {
    this.activeYear = localStorage.getItem('activeYear');
  }

  ngOnInit() {
    this.getReport();

    this.purgeReleaseByMinistry = this.fb.group({
      mdaId: ['', Validators.required],
      year: ('' + this.activeYear)
    });

    this.purgeReleaseByQuarter = this.fb.group({
      mdaId: ['', Validators.required],
      quarter: ['', Validators.required],
      year: ('' + this.activeYear)
    });

    this.getMda();
  }

  getReport() {
    const data = gql`
    query releaseByYear($year: String!) {
      releaseByYear(year: $year) {
          quarter
          title
          released
          utilized
          budget
          year
          mda{
            id
            name
            shortcode
          }
        }
      }
    `;
    this.crud.pullData(data, { year: this.activeYear + '' })
      .subscribe((result: any) => {
        this.report = [];
        let q1 = result.data.releaseByYear.filter(e => e.quarter == 1);
        let q2 = result.data.releaseByYear.filter(e => e.quarter == 2);
        let q3 = result.data.releaseByYear.filter(e => e.quarter == 3);
        let q4 = result.data.releaseByYear.filter(e => e.quarter == 4);
        console.log(result);

        for (let i = 0; i < result.data.releaseByYear.length; i++) {
          this.report.push({
            q1: q1[i],
            q2: q2[i],
            q3: q3[i],
            q4: q4[i],
          });

        }
        console.log(this.report);

        // result.data.releaseByYear.forEach(element => {
        // });
      });
  }

  getMda() {
    const data = gql`
      {
        mdas{
          id
          name
          shortcode
        }
      }
    `;
    this.crud.pullData(data, {})
      .subscribe((result: any) => {
        this.ministries = result.data.mdas;
      });
  }

  cummulativeResult(a, b, c, d) {
    let q1 = 0;
    let q2 = 0;
    let q3 = 0;
    let q4 = 0;
    if (a) {
      q1 = a;
    }
    if (b) {
      q2 = b;
    }
    if (c) {
      q3 = c;
    }
    if (d) {
      q4 = d;
    }

    return (q1 + q2 + q3 + q4);
  }

  getRate(released, utilized) {
    return ((utilized / released) * 100).toFixed(2);
  }
  openPurgeModal(purge) {
    switch (purge) {
      case 'mda':
        this.openMinistryPurgeModal();
        break;
      default:
        this.openPurgeAllModal();
        break;
    }
  }

  openPurgeAllModal() {
    $('#purgeAllReports').modal('show');
  }

  openMinistryPurgeModal() {
    $('#purgeReportsByMinistry').modal('show');
  }

  openQuarterPurgeModal() {
    // this.selectedQuater = data;
    // console.log(this.selectedQuater);
    $('#purgeByCode').modal('show');
  }
  uploadRelease() {
    const initialState = {
      // data: { outputId: data.id },

    }
    const modalRef = this.modalService.show(OagfReportManagerComponent, { class: 'modal-lg', initialState });
    modalRef.content.onClose.subscribe(result => {
      this.getReport();
    })
  }
  purgeByMinistry() {
    const data = gql`
      query deleteReleaseByMDAYear($year:String!, $mdaId:ID!) {
        deleteReleaseByMDAYear( year:$year, mdaId:$mdaId) {
          id
        }
      }
    `;
    this.crud.pullData(data, this.purgeReleaseByMinistry.value)
      .subscribe(result => {
        if (!result.data['deleteReleaseByMDAYear']) {
          this.toastr.error('Oops, something went wrong, kindly try again', 'Unsuccessful');
        } else {
          this.getReport();
          $('#purgeReportsByMinistry').modal('hide');
          this.toastr.success('Budget Deleted', 'Successful');
        }
      },
        (err => this.toastr.error('Oops, something went wrong, kindly try again', 'Unsuccessful')));
  }

  purgeAll() {
    const data = gql`
    query deleteReleaseByYear($year:String!) {
      deleteReleaseByYear(year:$year) {
        id
      }
    }
    `;
    this.crud.pullData(data, { year: this.activeYear + '' })
      .subscribe(result => {
        if (!result.data['deleteReleaseByYear']) {
          this.toastr.error('Oops, something went wrong, kindly try again', 'Unsuccessful');
        } else {
          this.getReport();
          $('#purgeAllReports').modal('hide');
          this.toastr.success('Report Deleted', 'Successful');
        }
      },
        (err => this.toastr.error('Oops, something went wrong, kindly try again', 'Unsuccessful')));
  }

  purgeByQuarter() {
    const data = gql`
    query deleteReleaseByQuarter($year:String!, $mdaId:ID!, $quarter:String!) {
      deleteReleaseByQuarter(year:$year, quarter:$quarter, mdaId:$mdaId) {
        id
      }
    }
    `;
    this.crud.pullData(data, this.purgeReleaseByQuarter.value)
      .subscribe(result => {
        if (!result.data['deleteReleaseByQuarter']) {
          this.toastr.error('Oops, something went wrong, kindly try again', 'Unsuccessful');
        } else {
          this.getReport();
          $('#purgeByCode').modal('hide');
          this.toastr.success('Reports Deleted', 'Successful');
        }
      },
        (err => this.toastr.error('Oops, something went wrong, kindly try again', 'Unsuccessful')));
  }
}
