import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import gql from 'graphql-tag';
import { ToastrService } from 'ngx-toastr';
import { CrudService } from 'src/app/service/crud.service';
import { BudgetManagementComponent } from '../budget-management/budget-management.component';
import { BsModalService } from 'ngx-bootstrap';
import { ChangeDetectorRef } from '@angular/core';
declare var $: any;

@Component({
  selector: 'app-budget-presentation',
  templateUrl: './budget-presentation.component.html',
  styleUrls: ['./budget-presentation.component.scss']
})
export class BudgetPresentationComponent implements OnInit {
  filedata: any;
  budget: any = [];
  temp: any;
  pageSize = 10;
  p = 1;
  editLineItem: FormGroup;
  purgeBudgetByMinistry: FormGroup;
  purgeBudgetByCode: FormGroup;
  selectedBudgetLineItem: any;
  ministries: any;
  purge: any;
  currentCode: any;
  budgetItem: any;
  budgetType;
  activeYear;
  active: string;
  uploadedMinistry: any;
  mdaFilter: any;
  totalCount: any;
  pageInfo: any;
  budgetList: any;
  startCount: number = 1;
  endCount: number = 10;
  getData: any = {};
  constructor(private crud: CrudService, private modalService: BsModalService, private fb: FormBuilder, private toastr: ToastrService, private cd: ChangeDetectorRef) {
    this.activeYear = localStorage.getItem('activeYear');
  }

  ngOnInit() {
    this.editLineItem = this.fb.group({
      budget_code: ['', Validators.required],
      budget_line_item: ['', Validators.required],
      amount: ['', Validators.required]
    });

    this.purgeBudgetByMinistry = this.fb.group({
      mda: ['', Validators.required]
    });

    this.purgeBudgetByCode = this.fb.group({
      budget_code: ['', Validators.required]
    });
    this.getMda();
    this.getData = {
      "first": 10,
      year: this.activeYear
    }
    this.getBudget(this.getData);
  }

  ngAfterViewInit() {
  }

  openPurgeModal(purge) {
    switch (purge) {
      case 'mda':
        this.openMinistryPurgeModal();
        break;
      default:
        this.openPurgeAllModal();
        break;
    }
  }

  get selectedLineItem() {
    return {
      budget_code: this.editLineItem.get('budget_code'),
      budget_line_item: this.editLineItem.get('budget_line_item'),
      amount: this.editLineItem.get('amount')
    };
  }

  getBudget(dataSet) {

    const data = gql`
      query budgetByYear($year: String!,$after: ID, $before: ID, $first: Int,
        $last: Int){
          budgetByYear(year: $year, after:$after,before: $before,first :$first, last: $last){
          totalCount
    pageInfo{
            endCursor,
            hasNextPage,
            hasPreviousPage,
            startCursor
          }
		      edges{
		        node{
          id
          budget_line_item
          budget_code
          amount
          year
          mda{
            id
            name
            shortcode
          }
        }
          }
        }
      }
    `;
    this.crud.pullData(data, dataSet).subscribe((e: any) => {

      this.totalCount = e.data['budgetByYear'].totalCount;
      this.budgetList = e.data['budgetByYear'].edges;
      console.log(this.budgetList);

      this.pageInfo = e.data['budgetByYear'].pageInfo;
      // this.cd.detectChanges();

      // this.results = true
      // this.budget = result.data.budgetByYear;
      this.temp = e.data.budgetByYear;
      this.budgetType = 'presentation'
      this.cd.detectChanges();

      // this.uploadedMinistries()
      // setTimeout(() => {
      //   $('#budgettable').DataTable()
      // }, 1000)
    });
  }
  previousPage() {

    this.getData = {
      "last": 10,
      "year": this.activeYear,

      "before": this.pageInfo.startCursor
    }
    this.getBudget(this.getData)
    this.startCount -= 10;
    this.endCount -= 10;
  }

  nextPage() {

    let searchData = {
      "first": 10,
      "year": this.activeYear,

      "after": this.pageInfo.endCursor

    }
    this.getBudget(searchData);
    this.startCount += 10;
    this.endCount += 10;
  }

  selectBudgetLineItem(i) {
    this.currentCode = this.budget[i].id;
    this.editLineItem.value.budget_code = this.budget[i].budget_code;
    this.editLineItem.value.budget_line_item = this.budget[i].budget_line_item;
    this.editLineItem.value.amount = this.budget[i].amount;
    this.selectedBudgetLineItem = this.budget[i];
    $('#updatItem').modal('show');
  }

  openPurgeAllModal() {
    $('#purgeAll').modal('show');
  }

  openMinistryPurgeModal() {
    $('#purgeByMinistry').modal('show');
  }

  openCodePurgeModal(data) {
    this.budgetItem = data;
    console.log(this.budgetItem);
    $('#purgeByCode').modal('show');
  }

  updateBudget() {
    const query = gql`
      mutation editBudget($id: ID!, $budget_code: String!, $budget_line_item: String!, $amount: String!) {
        editBudget(id: $id, budget_code:$budget_code, budget_line_item:$budget_line_item, amount:$amount) {
          id
        }
      }
    `;
    this.editLineItem.value.id = this.currentCode;

    this.crud.postData(query, this.editLineItem.value)
      .subscribe(
        (res: any) => {
          this.toastr.success('Budget Line Item edited successfully', 'Success');
          $('#updateItem').modal('hide');
          this.getBudget(this.getData);
        },
        (error) => {
          this.toastr.error('Budget Line Item edited unsuccessfuly, kindly try again', 'Unsuccessful');
        });
  }

  submitPurgeBudgetByMinistry(mda) {
    const data = gql`
      query deleteBudgetByMDAYear($year:String!, $mdaId:ID!) {
        deleteBudgetByMDAYear( year:$year, mdaId:$mdaId) {
          id
        }
      }
    `;
    this.crud.pullData(data, { year: this.activeYear, mdaId: mda })
      .subscribe(result => {
        if (!result.data['deleteBudgetByMDAYear']) {
          this.toastr.error('Oops, something went wrong, kindly try again', 'Unsuccessful');
        } else {
          this.getBudget(this.getData);
          this.toastr.success('Budget Deleted', 'Successful');
        }
      });
  }

  purgeAll() {
    const data = gql`
    query deleteBudgetByYear($year:String!) {
      deleteBudgetByYear(year:$year) {
        id
      }
    }
    `;
    this.crud.pullData(data, { year: this.activeYear })
      .subscribe(result => {
        if (!result.data['deleteBudgetByYear']) {
          this.toastr.error('Oops, something went wrong, kindly try again', 'Unsuccessful')
        } else {
          this.getBudget(this.getData);
          this.toastr.success('Budget Deleted', 'Successful')
        }
      });
  }

  deleteBudgetLineItem() {
    const data = gql`
    query deleteBudget($id:ID!) {
      deleteBudget(id:$id) {
        id
      }
    }
    `;
    this.crud.pullData(data, { id: this.budgetItem.id })
      .subscribe(result => {
        if (!result.data['deleteBudget']) {
          this.toastr.error('Oops, something went wrong, kindly try again', 'Unsuccessful');
        } else {
          this.getBudget(this.getData);
          $('#purgeByCode').modal('hide');
          this.toastr.success('Budget Deleted', 'Successful');
        }
      });
  }

  getMda() {
    const data = gql`
      {
        mdas{
          id
          name
          shortcode
        }
      }
    `;
    this.crud.pullData(data, {})
      .subscribe((result: any) => {
        this.ministries = result.data.mdas;
        this.getSingleBudget(this.ministries[0])
      });
  }

  uploadRelease() {
    this.active = 'releases';
  }

  uploadBudget() {
    const initialState = {
      // data: { outputId: data.id },

    }
    const modalRef = this.modalService.show(BudgetManagementComponent, { class: 'modal-lg', initialState });
    modalRef.content.onClose.subscribe(result => {
      this.getBudget(this.getData);
    })
  }
  uploadedMinistries() {
    let min = this.ministries.filter((mda) => {
      return this.checkMda(mda);
    })
    this.uploadedMinistry = min

  }

  checkMda(mda) {
    let status = false
    this.temp.forEach(element => {
      if (element.mda.id == mda.id)
        status = true
    });
    return status
  }

  filterBudget(mda) {
    if (mda === 'All Budget') {
      this.mdaFilter = 'All Budget'
      this.budget = this.temp;
      return
    }
    this.mdaFilter = mda.shortcode;
    let temp = this.temp.filter(e => e.mda.id === mda.id);
    this.budget = temp;
  }

  getSingleBudget(mda) {
    this.mdaFilter = mda.shortcode
    const data = gql`
    query budgetByMDAYear($year: String!, $mdaId: ID!) {
      budgetByMDAYear(year: $year, mdaId: $mdaId) {
          id
          budget_line_item
          budget_code
          amount
          year
          mda{
            id
            name
            shortcode
          }
        }
      }
    `;
    this.crud.pullData(data, { year: this.activeYear, mdaId: mda.id })
      .subscribe((result: any) => {
        this.budget = result.data.budgetByMDAYear;
        this.budgetType = 'presentation'
        this.cd.detectChanges();
      });
  }
}
