  async selectIndicator(item, project) {
    // 

    await this.sp[4].mandates[0].indicators.forEach(element => {
      if (item.title == element.title) {
        element.projects.push(project);
      }
    });
    await this.projects.forEach(element => {
      if (element.name == project.name) {
        element.indicator = item.title;
      }
    });

    localStorage.setItem('projects', JSON.stringify(this.projects))
    localStorage.setItem('sp', JSON.stringify(this.sp))
    this.toast.success('Indicator assigined successfully')
  }

