import { Component, OnInit } from '@angular/core';
import gql from 'graphql-tag';
import { FormBuilder } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';
import { BsModalRef } from 'ngx-bootstrap';
import { Router } from '@angular/router';
import { CrudService } from '../../../service/crud.service';
import { Subject } from 'rxjs';

@Component({
  selector: 'app-move',
  templateUrl: './move.component.html',
  styleUrls: ['./move.component.scss']
})
export class MoveComponent implements OnInit {
  indicators: any;
  ind: any;
  data: any;
  postData: any = {};
  onClose: any;

  constructor(private fb: FormBuilder, private toastr: ToastrService, public activeModal: BsModalRef, private router: Router, private crudService: CrudService) {
  }

  ngOnInit(): void {
    this.onClose = new Subject();
    //console.log(this.ind);

    this.postData.outputId = this.data.id
    this.postData.indicatorId = this.ind;
    //console.log(this.postData);


  }
  move() {
    //console.log(this.indicators);


    let query = gql`
    mutation move($outputId: ID!, $indicatorId: ID!) {
      move(outputId: $outputId, indicatorId:$indicatorId ) {
        id
        title
        baseline
        actual
        target
      }
      }
    `

    this.crudService.postData(query, this.postData)
      .subscribe(
        (res: any) => {

          this.toastr.success("Output Indicator moved successfully", "Success")
          this.activeModal.hide()
          this.onClose.next(false);
        },
        (error) => {
          this.toastr.error("Output Indicator moved unsuccessfuly, kindly try again", "Unsuccessful")
        });
  }
}
