  initializeGuage(data) {
    const currentGuage = document.getElementsByClassName('gauge');

    for (let i = 0; i < data.length; i++) {
      const gauge = this.Gauge(currentGuage[i]);
      const target = this.prepareTarget(data[i].target).annual;

      const percent = (this.crud.calculatePercantage(data[i].actual, data[i].baseline, target, data[i].aggregation)) / 100;

      gauge.value(percent);
    }
  }

  getPercentage(data) {
    let percentage = 0;
    percentage = (data.actual / (data.target - data.baseline)) * 100;
    return percentage;
  }

  setColor(data) {
    this.initializeGuage(this.outputs);
    const target = this.prepareTarget(data.target).target;
    const r = (this.crud.calculatePercantage(parseFloat(data.actual), parseFloat(data.baseline), parseFloat(target), data.aggregation));
    // console.log(r);

    if (r <= 33.3) {
      return 'red';
    }
    if (r > 33.3 && r <= 66.6) {
      return '#ffbf00';
    }
    if (r > 66.6) {
      return 'green';
    }
  }


  setColorPercentage(data) {
    let r = data;

    if (r <= 33.3) {
      return 1
    }
    if (r > 33.3 && r <= 66.6) {
      return 2
    }
    if (r > 66.6) {
      return 3
    }
  }
  Gauge(el) {

    // ##### Private Properties and Attributes

    var element,      // Containing element for the info component
      data,         // `.gauge--data` element
      needle,       // `.gauge--needle` element
      value = 0.0,  // Current gauge value from 0 to 1
      prop;         // Style for transform

    // ##### Private Methods and Functions

    var setElement = function (el) {
      // Keep a reference to the various elements and sub-elements
      element = el;
      data = element.querySelector(".gauge--data");
      needle = element.querySelector(".gauge--needle");
    };

    var setValue = function (x) {
      value = x;
      var turns = -0.5 + (x * 0.5);
      data.style[prop] = "rotate(" + turns + "turn)";
      needle.style[prop] = "rotate(" + turns + "turn)";
    };

    // ##### Object to be Returned

    var exports = {
      element: function (el) {
        if (!arguments.length) { return element; }
        setElement(el);
        return this;
      },
      value: function (x) {
        if (!arguments.length) { return value; }
        setValue(x);
        return this;
      }
    };

    // ##### Public API Methods

    // ##### Initialization

    var body = document.getElementsByTagName("body")[0];
    ["webkitTransform", "mozTransform", "msTransform", "oTransform", "transform"].
      forEach(function (p) {
        if (typeof body.style[p] !== "undefined") { prop = p; }
      }
      );

    if (arguments.length) {
      setElement(el);
    }

    return exports;

  };
