  calcPercentage(a, b) {
    // //console.log(a / b * 100 + '%');

    return a / b * 100 + '%';
  }
  calcuPercentage(a, b) {
    let r = a / b * 100;


    if (r <= 33.3) {
      return 1;
    }
    if (r > 33.3 && r <= 66.6) {
      return 2;
    }
    if (r > 66.6) {
      return 3;
    }
  }
