  constructor(private fb: FormBuilder, private crud: CrudService, private toastr: ToastrService, public activeModal: BsModalRef, private route: Router) {
    this.getAllMda();
    this.facilityForm = new FormGroup({
      'classification': new FormControl(null, Validators.required),
    });

    this.ownerForm = new FormGroup({
      'owner_name': new FormControl(null, Validators.required)
    });

    let year = new Date().getFullYear();
    year = year + 1;
    this.selectedDate = localStorage.getItem('activeYear');
    for (let index = 0; index < 10; index++) {
      this.year.push((year - index) + '');
    }
  }

  ngOnInit() {
  }

  onFileChange() {
    this.tbody = [];
    const target: DataTransfer = <DataTransfer>(this.excelFile.target);
    this.fileName = this.excelFile.target.files[0].name;
    if (target.files.length !== 1) throw new Error('Cannot use multiple files');

    const reader: FileReader = new FileReader();
    reader.onload = (e: any) => {
      /* read workbook */
      const bstr: string = e.target.result;
      const wb: XLSX.WorkBook = XLSX.read(bstr, { type: 'binary' });

      /* grab first sheet */
      const wsname: string = wb.SheetNames[0];
      const ws: XLSX.WorkSheet = wb.Sheets[wsname];

      /* save data */
      this.filedata = XLSX.utils.sheet_to_json(ws, { header: 1 });

      this.filedata.forEach(e => {
        this.tbody.push({
          no: e[0],
          title: e[1],
          mda: e[2],
          budget: e[3],
          released: e[4],
          utilized: e[5],
        });
      });
    };
    reader.readAsBinaryString(target.files[0]);
    this.submitOwnerForm();
  }

  openRowDeleteModal(i) {
    this.rowIndex = i;
    this.tbody.splice(this.rowIndex, 1);
    // $('#deleteRowModal').modal('show');
  }


  get classification() {
    return this.facilityForm.get('classification');
  }

  get owner_name() {
    return this.ownerForm.get('owner_name');
  }

  setYear(event) {
    this.selectedDate = event.target.value;
  }

  submitFacilityForm() {
    this.active++;
    this.completed++;
    this.current++;

    if (this.facilityForm.value.classification == 1) {
      this.ministryName = 'First Quarter';
    } else if (this.facilityForm.value.classification == 2) {
      this.ministryName = 'Second Quarter';
    } else if (this.facilityForm.value.classification == 3) {
      this.ministryName = 'Third Quarter';
    } else if (this.facilityForm.value.classification == 4) {
      this.ministryName = 'Fourth Quarter';
    }
  }

  submitOwnerForm() {
    this.active++;
    this.completed++;
    this.current++;
  }

  setActive() {
    this.active--;
    this.completed--;
    this.current--;
  }

  getAllMda() {
    const data = gql`
      {
        mdas{
          id
          name
          shortcode
        }
      }
    `;
    this.crud.pullData(data, {})
      .subscribe((result: any) => {
        this.ministries = result.data.mdas;
      });
  }

  addMda(index, id) {
    //console.log(id);

    this.tbody[index].mdaId = id.target.value;
  }
