import { StrategicPlanComponent } from './strategic-plan/strategic-plan.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { DashboardComponent } from './dashboard/dashboard.component';
import { ProjectsComponent } from './projects/projects.component';
import { ReleasesComponent } from './reports/releases/releases.component';
import { ContractComponent } from './contract/contract.component';
import { LoginComponent } from './login/login.component';
import { AuthGuard } from './auth.guard';
import { DashboardContainerComponent } from './dashboard-container/dashboard-container.component';
import { UserSettingsComponent } from './user-settings/user-settings.component';
import { MinistrySettingsComponent } from './ministry-settings/ministry-settings.component';
import { ActivityDetailsComponent } from './activity-details/activity-details.component';
import { ContractorLogframeComponent } from './reports/contractor-logframe/contractor-logframe.component';
import { FieldLogframeComponent } from './reports/field-logframe/field-logframe.component';
import { CitizenLogframeComponent } from './reports/citizen-logframe/citizen-logframe.component';
import { ProjectDetailsComponent } from './projects/project-details/project-details.component';
import { MessageLogComponent } from './message-log/message-log.component';
import { NotificationsComponent } from './message-log/notifications/notifications.component';
import { BudgetManagementComponent } from './fiscal/budget-management/budget-management.component';
import { BudgetPresentationComponent } from './fiscal/budget-presentation/budget-presentation.component';
import { OagfReportComponent } from './fiscal/oagf-report/oagf-report.component';
import { OagfReportManagerComponent } from './fiscal/oagf-report-manager/oagf-report-manager.component';
import { FiscalManagementComponent } from './fiscal/fiscal-management/fiscal-management.component';
import { IndicatorSettingsComponent } from './user-settings/indicator-settings/indicator-settings.component';
import { SearchComponent } from './search/search.component';
import { SpComponent } from './strategic-plan/sp/sp.component';
import { DeliverableManagementComponent } from './deliverable-management/deliverable-management.component';

const routes: Routes = [
  {
    path: '',
    redirectTo: 'home',
    pathMatch: 'full'
  },
  { path: 'login', component: LoginComponent },
  {
    path: 'home',
    component: DashboardContainerComponent,
    canActivate: [AuthGuard],
    children: [
      { path: '', redirectTo: 'dashboard', pathMatch: 'full' },
      { path: 'result-tree', component: SpComponent, data: { title: 'Federal Government Priorities' } },
      { path: 'dashboard', component: DashboardComponent, data: { title: 'Analytics' } },
      { path: 'projects', component: ProjectsComponent, data: { title: 'Activities' } },
      { path: 'contracts', component: ContractComponent, data: { title: 'Contracts' } },
      { path: 'review', component: ReleasesComponent, data: { title: 'Desk Reviews' } },
      { path: 'search/:query', component: SearchComponent, data: { title: 'Search' } },
      { path: 'fiscal-management', component: FiscalManagementComponent, data: { title: 'Budget Management' } },
      { path: 'deliverable-management', component: DeliverableManagementComponent, data: { title: 'Deliverable Management' } },
      // { path: 'budget-management', component: BudgetManagementComponent, data: { title: 'Budget Management' } },
      // { path: 'budget-presentation', component: BudgetPresentationComponent, data: { title: 'Budget Presentation' } },
      // { path: 'oagf-report-management', component: OagfReportManagerComponent, data: { title: 'Release Management' } },
      // { path: 'oagf-report', component: OagfReportComponent, data: { title: 'OAFG Report' } },
      // { path: 'settings/user', component: UserSettingsComponent, data: { title: 'User Settings' }, },
      { path: 'settings/ministry', component: MinistrySettingsComponent, data: { title: 'Ministry Setting' }, },
      { path: 'settings/system', component: UserSettingsComponent, data: { title: 'System Setting' }, },
      { path: 'settings/priorities', component: IndicatorSettingsComponent },
      { path: 'message/log', component: MessageLogComponent, data: { title: 'Message Log' }, },
      { path: 'notifications/log', component: NotificationsComponent, data: { title: 'Notifications Log' }, },
      { path: 'sp/activity-details', component: ActivityDetailsComponent, data: { title: 'Activity Details' }, },
      { path: 'contractor-logframe', component: ContractorLogframeComponent, data: { title: 'Contractor Logframe' }, },
      { path: 'field-logframe', component: FieldLogframeComponent, data: { title: 'Field Logframe' }, },
      { path: 'citizen-logframe', component: CitizenLogframeComponent, data: { title: 'Citizen Logframe' }, },
      { path: 'project/project-report-log', component: ProjectDetailsComponent, data: { title: 'Project Report Log' }, },
    ]
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes, {useHash: true})],
  exports: [RouterModule]
})
export class AppRoutingModule { }
