import { Injectable } from '@angular/core';
import { Router, CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot } from '@angular/router';

// import { ToastrService } from 'ngx-toastr';
// import { LocalStorageService } from 'ngx-webstorage';

@Injectable()
export class AuthGuard implements CanActivate {
    constructor(private router: Router) { }

    canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot) {
        if (localStorage.getItem('token')) {
            // logged in so return true
            return true;
        }

        // not logged in so redirect to login page with the return url
        // this.toast.error('Please log in');
        this.router.navigate(['/login']);
        return false;
    }
    
    canActivateDash(route: ActivatedRouteSnapshot, state: RouterStateSnapshot) {
        let permissions = JSON.parse(localStorage.getItem('permissions'))
        if (permissions.includes('dashboard')) {
            return true;
        }
        this.router.navigate(['/home/sp']);
        return false;
    }
    canActivatePm(route: ActivatedRouteSnapshot, state: RouterStateSnapshot) {
        let permissions = JSON.parse(localStorage.getItem('permissions'))
        if (permissions.includes('pm')) {
            return true;
        }
        this.router.navigate(['/home/sp']);
        return false;
    }
    canActivateFm(route: ActivatedRouteSnapshot, state: RouterStateSnapshot) {
        let permissions = JSON.parse(localStorage.getItem('permissions'))
        if (permissions.includes('fm')) {
            return true;
        }
        this.router.navigate(['/home/sp']);
        return false;
    }
    canActivateRep(route: ActivatedRouteSnapshot, state: RouterStateSnapshot) {
        let permissions = JSON.parse(localStorage.getItem('permissions'))
        if (permissions.includes('rep')) {
            return true;
        }
        this.router.navigate(['/home/sp']);
        return false;
    }
    canActivateSettings(route: ActivatedRouteSnapshot, state: RouterStateSnapshot) {
        let permissions = JSON.parse(localStorage.getItem('permissions'))
        if (permissions.includes('settings')) {
            return true;
        }
        this.router.navigate(['/home/sp']);
        return false;
    }
    unGuard(route: ActivatedRouteSnapshot, state: RouterStateSnapshot) {
        if (localStorage.getItem('token')) {
            this.router.navigate(['/home/sp']);
            return true;
        }
    }
}