import gql from 'graphql-tag';
import { Component, OnInit, TemplateRef } from '@angular/core';
// import { IMultiSelectOption, IMultiSelectSettings, IMultiSelectTexts } from 'angular-2-dropdown-multiselect';
import { FormControl, FormGroup, Validators, FormBuilder } from '@angular/forms';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { Router } from '@angular/router';
import { CursorError } from '@angular/compiler/src/ml_parser/lexer';
import { prependListener } from 'cluster';
import { CrudService } from 'src/app/service/crud.service';
import { ToastrService } from 'ngx-toastr';
declare const Chart: any;

@Component({
  selector: 'app-indicator-settings',
  templateUrl: './indicator-settings.component.html',
  styleUrls: ['./indicator-settings.component.scss']
})
export class IndicatorSettingsComponent implements OnInit {
  accordionData;
  optionsModel: number[];
  // myOptions: IMultiSelectOption[];
  newMandate: FormGroup;
  modalRef: BsModalRef;
  config = {
    animated: true
  };
  customClass = 'customClass';
  preffered = [];
  // Settings configuration
  // mySettings: IMultiSelectSettings = {
  //   enableSearch: true,
  //   checkedStyle: 'fontawesome',
  //   buttonClasses: 'btn btn-default btn-block',
  //   dynamicTitleMaxItems: 3,
  //   displayAllSelectedText: true
  // };

  // Text configuration
  // myTexts: IMultiSelectTexts = {
  //   checkAll: 'check all',
  //   uncheckAll: 'uncheck all',
  //   checked: 'item selected',
  //   checkedPlural: 'items selected',
  //   searchPlaceholder: 'Find',
  //   searchEmptyResult: 'Nothing found...',
  //   searchNoRenderText: 'Type in search box to see results...',
  //   defaultTitle: 'Select',
  //   allSelected: '',
  // };
  chart: any;
  p = 1;
  collection: any[];
  priorities: any;
  user: any;

  // tslint:disable-next-line:max-line-length
  constructor(private fb: FormBuilder, private crudService: CrudService, private modalService: BsModalService, private router: Router, private toastr: ToastrService) {
    this.newMandate = this.fb.group(this.mandateFormGroup());
    this.user = JSON.parse(localStorage.getItem('user'));
  }

  ngOnInit() {
    this.getPriorities();
    let pref = JSON.parse(localStorage.getItem('preffered'));
    if (pref != undefined) this.preffered = pref;
  }

  mandateFormGroup() {
    return {
      title: new FormControl('', [
        Validators.required
      ]),
      optionsModel: new FormControl('', [
        Validators.required
      ]),
      target: new FormControl('', [
        Validators.required
      ]),
      baseline: new FormControl('', [
        Validators.required
      ]),
      deadline: new FormControl('', [
        Validators.required,
        Validators.minLength(4),
        Validators.maxLength(4),
        Validators.min(2019),
        Validators.max(2023)
      ]),
    };
  }

  get mandateForm() {
    return {
      title: this.newMandate.get('title'),
      deadline: this.newMandate.get('deadline'),
      target: this.newMandate.get('target'),
      baseline: this.newMandate.get('baseline'),
      optionsModel: this.newMandate.get('optionsModel'),
    };
  }

  openModal(template: TemplateRef<any>) {
    this.modalRef = this.modalService.show(template, this.config);
  }

  getPriorities() {
    const data = gql`
    {
      priorities{
        id
        title
        mdas
        mdasList{
          id
        }
        indicators{
            id
            title
            ref
            type
            baseline
            actual
            target
            aggregation
            rt
            currency
            active
            mdaId
            driver
            outputs{
              id
              title
              ref
              baseline
              actual
              target
              aggregation
              rt
              currency
              active
              mdaId
            }
          }
          outputs{
            id
            title
            ref
            baseline
            actual
            target
            aggregation
            rt
            currency
            active
            mdaId
          }
        }
      }
  `;
    this.crudService.pullData(data, {})
      .subscribe((result: any) => {
        let priority = result.data.priorities;
        if (this.user.role.name === 'Admin') {
          this.priorities = priority;
          return;
        }

        this.priorities = priority.filter(e => {
          const mda = JSON.parse(e.mdas);
          return mda.includes(this.user.mda[0].id);
        });
      });
  }

  addToPreffered(ind, type) {
    if (this.preffered.length === 12) {
      this.toastr.error('you can only select 12');
      return;
    }
    if (this.preffered.length > 0) {
      this.preffered = this.preffered.filter((e) => e.id !== ind.id);
      localStorage.setItem('preffered', JSON.stringify(this.preffered));
      this.preffered.push(ind);
      this.toastr.success('Added Successfully', 'Success');
    }
  }

  checkPref(data) {
    let check = false;
    this.preffered.includes(element => {
      if (element.id === data.id) {
        check = true;
      }
    });
    return check;
  }

  clearSetting() {
    if (localStorage.getItem('preffered')) {
      localStorage.removeItem('preffered');
      this.toastr.success('Your previous selection has been cleared', 'Success');
    }
    this.toastr.warning('Your do not have a previous selection', 'Information');
  }
}
