import { Component, Input, OnInit } from '@angular/core';
import gql from 'graphql-tag';
import { BsModalRef } from 'ngx-bootstrap/modal';
import { ToastrService } from 'ngx-toastr';
import { Subject } from 'rxjs';
import { CrudService } from 'src/app/service/crud.service';

@Component({
  selector: 'app-report-details',
  templateUrl: './report-details.component.html',
  styleUrls: ['./report-details.component.scss']
})
export class ReportDetailsComponent implements OnInit {
  @Input() data;
  onClose: any;
  images
  constructor(public activeModal: BsModalRef, private crud:CrudService, private toastr:ToastrService) { 
  }
  
  ngOnInit() {
    this.images = JSON.parse(this.data.images)
    this.onClose = new Subject();
  }

  changeLogrameStatus(data){
    let query = gql`
      query changeLogframeStatus($id:ID!, $logframeStatus:String!){
        changeLogframeStatus(id:$id, logframeStatus:$logframeStatus){
          id
        }
      }
    `

    //console.log({id:this.data.id,logframeStatus:data});
    
    this.crud.pullData(query, {id:this.data.id,logframeStatus:data}).subscribe((res:any)=>{
      if (!res.error) {
        this.onClose.next(false);
        this.toastr.success('Logframe Status has been '+data, 'Logframe');
      }else{
        this.toastr.error('Logframe Status submission was unsuccessful '+data, 'Logframe');
      }
    })
  }
}
