import { Subject } from 'rxjs';
import { filter } from 'rxjs/operators';
import { Component, OnInit, Input } from '@angular/core';




import { ToastrService } from 'ngx-toastr';
import { Router } from '@angular/router';
import { BsModalRef } from 'ngx-bootstrap/modal';

import gql from 'graphql-tag';
import { CrudService } from '../../service/crud.service';

@Component({
  selector: 'app-assign-mandate',
  templateUrl: './assign-mandate.component.html',
  styleUrls: ['./assign-mandate.component.css']
})
export class AssignMandateComponent implements OnInit {
  @Input() data;
  postData: any = {};
  submit: any;
  sh: any;
  project: any;
  selSI: any = []

  initiatives: any = [];
  onClose: any;
  help: any = [
    "Ensure you fill all fields",
    "You can tie the initiative to FEC memo now or at a later date"
  ]
  role: any;
  mda: any;
  dropdownSettings: { singleSelection: boolean; idField: string; textField: string; selectAllText: string; unSelectAllText: string; itemsShowLimit: number; allowSearchFilter: boolean; enableCheckAll: boolean; };
  mandates: any;
  constructor(private toastr: ToastrService, private crud: CrudService, public activeModal: BsModalRef, private router: Router) {
    if (JSON.parse(localStorage.getItem('initiatives')) != null)
      this.initiatives = JSON.parse(localStorage.getItem('initiatives'));

    this.role = JSON.parse(localStorage.getItem('role'));
    this.mda = JSON.parse(localStorage.getItem('mda'));
  }


  ngOnInit() {
    //console.log(this.data.title);

    this.onClose = new Subject();
    this.dropdownSettings = {
      singleSelection: false,
      idField: 'id',
      textField: 'title',
      selectAllText: 'Select All',
      unSelectAllText: 'UnSelect All',
      itemsShowLimit: 5,
      allowSearchFilter: true,
      enableCheckAll: false
    };
    this.pullMandates();
  }

  onItemSelect(item: any) {
    // this.mdaForm.value.mdas = []
    // this.selectedItems.forEach(e => {
    //   this.mdaForm.value.mdas.push(e.id)
    // });
    this.mandates.forEach(element => {
      if (item.id == element.id) this.selSI = JSON.parse(element.si)
    });
    console.log(this.selSI);

  }

  onItemDeSelect(item: any) {
    // this.mdaForm.value.mdas = []
    // this.selectedItems.forEach(e => {
    //   this.mdaForm.value.mdas.push(e.id)
    // });
  }
  onFilterChange(item: any) {

  }
  pullMandates() {


    let query = gql`
  query {
    mandates {
        id
        si
        title
    }

  }
   `
    this.crud.pullData(query, {}).subscribe(result => {
      this.mandates = result.data['mandates'];

    })
  }
  create() {
    this.submit = true;
    console.log(this.postData);

    this.postData.projectId = this.project.id;
    this.postData.mandates = JSON.stringify(this.postData.mandates);
    let query = gql`
      mutation assignMandates(
        $mandates: String!, $projectId: ID!, $si: String
        ){
          assignMandates(mandates: $mandates, projectId: $projectId, si: $si) {
            id 
        }
        }
      `
    this.crud.postData(query, this.postData).subscribe((result) => {
      if (!result.data['assignMandates']) {
        // this.error = result.errors[0].message;

      } else {
        this.toastr.success("Deliverable successful assigned", "Success")
        this.onClose.next(false);
        this.activeModal.hide()
      }
    }, (error) => {
      this.toastr.error("Something went wrong, kindly try again", "Unsuccessful")
    });
  }
  // this.initiatives.push(this.postData);
  // localStorage.setItem('initiatives', JSON.stringify(this.initiatives))


  // getLga(id) {
  //   this.states.forEach(el => {
  //     if (el.state.name == this.postData.state)
  //       this.lgas = el.state.locals;
  //   });
  //   //console.log(this.lgas);

  // }
}
