import { Component, OnInit, Input } from '@angular/core';




import { ToastrService } from 'ngx-toastr';
import { Router } from '@angular/router';
import { BsModalRef } from 'ngx-bootstrap/modal';
import gql from 'graphql-tag';
import { CrudService } from 'src/app/service/crud.service';
import { Subject } from 'rxjs';

@Component({
  selector: 'app-create-outputs',
  templateUrl: './create-outputs.component.html',
  styleUrls: ['./create-outputs.component.css']
})
export class CreateOutputsComponent implements OnInit {
  data: any;
  type: any = '';
  postData: any = { targetYear1: {}, targetYear2: {}, targetYear3: {}, targetYear4: {} };
  submit: any;
  sh: any;
  states: any;
  title;
  userObj: any;
  onClose: any;
  mdas: any;
  constructor(private toastr: ToastrService, public activeModal: BsModalRef, private router: Router, private crudService: CrudService) {
  }

  ngOnInit() {
    this.title = this.data.title;
    this.onClose = new Subject();
    this.mdas = JSON.parse(localStorage.getItem('mdas'))
    console.log(this.type);

  }
  create(data) {
    this.postData.indicatorId = this.data.indicatorId;
    this.postData.mandateId = this.data.mandateId;
    this.postData.outputType = this.type;




    let target = [];
    target.push({ year: 2020, ...this.postData.targetYear1 })
    target.push({ year: 2021, ...this.postData.targetYear2 })
    target.push({ year: 2022, ... this.postData.targetYear3 })
    target.push({ year: 2023, ...this.postData.targetYear4 })
    this.postData.target = JSON.stringify(target);


    delete this.postData.targetYear1;
    delete this.postData.targetYear2;
    delete this.postData.targetYear3;
    delete this.postData.targetYear4;
    delete this.postData.tag;

    console.log(this.postData);
    // this.submit = true;
    let query = gql`
    mutation createOutput($outputInput: outputInputType!) {
      createOutput(outputInput: $outputInput) {
        id

      }
      }
    `

    this.crudService.postData(query, { outputInput: this.postData })
      .subscribe(
        (res: any) => {

          this.toastr.success("Output Indicator successful Created", "Success")
          this.activeModal.hide()
          this.onClose.next(false);
        },
        (error) => {
          this.toastr.error("Output Indicator created unsuccessly, kindly try again", "Unsuccessful")
        });
  }
  calculateTarget() {
  }

  calculateTargets() {
    //console.log('calculating')
    if (this.postData.type == 'quan') {
      let base = (parseInt(this.postData.targetYear1.annual) - parseInt(this.postData.baseline)) / 4;
      //console.log(base);

      this.postData.targetYear1.annual = parseInt(this.postData.baseline) + (base * 1);
      this.postData.targetYear1.q1 = (this.postData.targetYear1.annual / 4) * 1
      this.postData.targetYear1.q2 = (this.postData.targetYear1.annual / 4) * 2
      this.postData.targetYear1.q3 = (this.postData.targetYear1.annual / 4) * 3
      this.postData.targetYear1.q4 = (this.postData.targetYear1.annual / 4) * 4
      this.postData.targetYear2.annual = parseInt(this.postData.baseline) + (base * 2);
      this.postData.targetYear2.q1 = this.postData.targetYear2.annual - ((this.postData.targetYear2.annual / 4) * 3)
      this.postData.targetYear2.q2 = this.postData.targetYear2.annual - ((this.postData.targetYear2.annual / 4) * 2)
      this.postData.targetYear2.q3 = this.postData.targetYear2.annual - ((this.postData.targetYear2.annual / 4) * 1)
      this.postData.targetYear2.q4 = this.postData.targetYear2.annual
      this.postData.targetYear3.annual = parseInt(this.postData.baseline) + (base * 3);
      this.postData.targetYear3.q1 = this.postData.targetYear3.annual - ((this.postData.targetYear3.annual / 4) * 3)
      this.postData.targetYear3.q2 = this.postData.targetYear3.annual - ((this.postData.targetYear2.annual / 4) * 2)
      this.postData.targetYear3.q3 = this.postData.targetYear3.annual - ((this.postData.targetYear3.annual / 4) * 1)
      this.postData.targetYear3.q4 = this.postData.targetYear3.annual
      this.postData.targetYear4.annual = parseInt(this.postData.baseline) + (base * 4);
      this.postData.targetYear4.q1 = this.postData.targetYear4.annual - ((this.postData.targetYear2.annual / 4) * 3)
      this.postData.targetYear4.q2 = this.postData.targetYear4.annual - ((this.postData.targetYear2.annual / 4) * 2)
      this.postData.targetYear4.q3 = this.postData.targetYear4.annual - ((this.postData.targetYear2.annual / 4) * 1)
      this.postData.targetYear4.q4 = this.postData.targetYear4.annual
      //console.log(parseInt(this.postData.baseline) + (base * 4));
      console.log(this.postData);

    }
  }
  getUser() {
    this.userObj = JSON.parse(localStorage.getItem('user'))
    return this.userObj
  }
}
