import { Component, OnInit, Input } from '@angular/core';
import { ToastrService } from 'ngx-toastr';
import { Router } from '@angular/router';
import gql from 'graphql-tag';
import { CrudService } from '../../../service/crud.service';
import { BsModalRef } from 'ngx-bootstrap';
import { Subject } from 'rxjs';
import states from '../../states.json';

@Component({
  selector: 'app-edit-project',
  templateUrl: './edit-project.component.html',
  styleUrls: ['./edit-project.component.css']
})
export class EditProjectComponent implements OnInit {
  @Input() data;
  postData: any = {};
  submit: any;
  sh: any;
  e: any;
  d: any
  states: any = states;

  budgets: any;
  mdas: any;
  userObj: any;
  contracts: any;
  outputIndicators: any;

  lgas: any;
  onClose: any;
  mda: any;
  role: any;
  activeYear: any;
  temp: any;
  selectedBudget: any;


  constructor(private toastr: ToastrService, private crud: CrudService, public activeModal: BsModalRef, private router: Router) {
    this.getMda();
    if (JSON.parse(localStorage.getItem('projects')) != null)
    this.activeYear = localStorage.getItem('activeYear')
    this.role = JSON.parse(localStorage.getItem('role'));
    this.mda = JSON.parse(localStorage.getItem('mda'));
  }

  ngOnInit() {
    this.getUserDetails();
    this.onClose = new Subject();
    this.mdas = JSON.parse(localStorage.getItem('mdas'));
    this.postData = this.data
    console.log(this.postData);
    
  }

  create() {
    parseFloat
    this.submit = true;
    // this.postData.budget = parseFloat(this.postData.budget);
    delete this.postData.__typename;
    delete this.postData.ref;
    delete this.postData.active;
    delete this.postData.output;
    delete this.postData.mda;
    delete this.postData.indicator;
    delete this.postData.logframes;
    delete this.postData.mandate;
    delete this.postData.images;
    // this.postData.mdaId = this.mda.id;
    const query = gql`
      mutation editProject(
        $projectInput: projectInputType!
        ){
          editProject(projectInput: $projectInput) {
            id
        }
        }
      `;
    this.postData.type = Number.parseInt(this.postData.type);
    console.log(this.postData);

    this.crud.postData(query, { projectInput: this.postData }).subscribe((result) => {
      if (!result.data['editProject']) {
        // this.error = result.errors[0].message;

      } else {
        this.toastr.success('Project successful Created', 'Success');
        this.onClose.next(false);
        this.activeModal.hide();
      }
    }, (error) => {
      this.toastr.error('Something went wrong, kindly try again', 'Unsuccessful');
    });
  }
  // this.projects.push(this.postData);
  // localStorage.setItem('projects', JSON.stringify(this.projects))


  getLga() {
    this.states.forEach(el => {
      if (el.state.name == this.postData.state)
        this.lgas = el.state.locals;
    });
    //console.log(this.lgas);
  }


  getOutputIndicator() {
    const query = gql`
      query outputsByMdas($mdaId: ID!) {
        outputsByMdas(mdaId: $mdaId) {
          id
          title
        }
      }
    `;

    this.crud.pullData(query, { mdaId: this.postData.mdaId })
      .subscribe(
        (res: any) => {
          this.outputIndicators = res.data.outputsByMdas;
          console.log(this.outputIndicators);
        },
        (error) => {

        });
  }

  getContracts() {
    const query = gql`
      query contractsByMdas($mdaId: ID!) {
        contractsByMdas(mdaId: $mdaId) {
          id
          title
        }
      }
    `;

    this.crud.pullData(query, { mdaId: this.postData.mdaId })
      .subscribe(
        (res: any) => {
          this.contracts = res.data.contractsByMdas;
          // console.log(this.contractIndicators);

        },
        (error) => {

        });
  }
  getUserDetails() {
    this.userObj = JSON.parse(localStorage.getItem('user'));
  }

  getMda() {
    const data = gql`
      {
        mdas{
          id
          name
          shortcode
        }
      }
    `;
    this.crud.pullData(data, {})
      .subscribe((result: any) => {
        this.mdas = result.data.mdas;
      });
  }

  getBudget() {
    const data = gql`
    query budgetByMDAYear($year: String!, $mdaId: ID!) {
      budgetByMDAYear(year: $year, mdaId: $mdaId) {
          id
          budget_line_item
          budget_code
          amount
        }
      }
    `;
    this.crud.pullData(data, { year: this.activeYear, mdaId: this.postData.mdaId })
      .subscribe((result: any) => {
        this.budgets = result.data.budgetByMDAYear;
        this.temp = result.data.budgetByMDAYear;
      });
  }

  setBudgetCost() {
    //   let budget = this.budgets;
    //   budget = budget.filter(e => e.id === this.postData.budgetId);
    //   this.postData.budget = budget[0].amount;
    //   console.log(this.postData.budget);
  }

  selectBudgetLineItem(data){
    this.selectedBudget = data;
    this.postData.budgetId = data.id
  }

  updateFilter(event) {
    let val = event.target.value;

    // filter our data
    
    const temp = this.temp.filter((d: any) => {
      let budget_code: any = d.budget_code
      let budget_line_item: any = d.budget_line_item
      // let ministry = d.mda.name
      // let phone = d.mda.name
      return budget_line_item.toLowerCase().indexOf(val) !== -1 || budget_line_item.indexOf(val) !== -1 || !val || budget_code.toLowerCase().indexOf(val) !== -1 || budget_code.indexOf(val) !== -1;
    });

    this.budgets = temp;
  }
}


