  calcPercentage(a, b, t, agg) {
    if (t == null) return null;
    let tar = this.prepareTarget(t, null).target;
    let r = this.crudService.calculatePercantage(a, b, tar, agg)

    if (r <= 33.3) {
      return 1;
    }
    if (r > 33.3 && r <= 66.6) {
      return 2;
    }
    if (r > 66.6) {
      return 3;
    }
  }

