  changeView(id, text) {
    this.currentViewText = text
    this.currentView = id;
    // this.filterData(id);
    if (this.currentView == 'so') {

      this.priorities = this.temp
      return
    }

    this.priorities = this.temp.filter((data) => {
      let mdas = JSON.parse(data.mdas)
      //console.log(mdas);
      if (mdas.length == 0) return false
      if (JSON.parse(data.mdas).includes(this.currentView)) {
        return true;
      }
    })

    //console.log(this.priorities);




  }
  filterData(id) {


    if (this.currentView == 'so') return false

    if (this.currentView == id)
      return false
    else
      return true
  }
  prepareTarget(data, index) {
    if (data == null) return [];
    let pre = JSON.parse(data);
    let rdata
    if (index == null) {
      pre.forEach(element => {
        if (element.year == this.activeYear)
          rdata = element
      });
      // console.log(rdata);

      return rdata;
    }

    return pre[index];
  }
