import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { BsModalService } from 'ngx-bootstrap';
import { ProjectDetailsModalComponent } from './modal/project-details-modal/project-details-modal.component';

@Component({
  selector: 'app-activity-details',
  templateUrl: './activity-details.component.html',
  styleUrls: ['./activity-details.component.scss']
})
export class ActivityDetailsComponent implements OnInit {
  p: number = 1;

  projects:any = [];
  mandate:any;
  outputs:any;
  temp: any = [];
  pageSize = 10;
  constructor(private modalService: BsModalService, private router:Router) { 
    
    if (history.state.mandate) {
      this.mandate = JSON.parse(history.state.mandate)
      // this.outputs = this.mandate.outputs
      this.outputs = this.mandate.outputs
      this.mandate.projects.forEach(el => {
        this.projects.push(el)
        this.temp.push(el)
      });
      //console.log(this.outputs);
      
    }
    else{
      this.router.navigate(['home/sp'])
    }
  }

  ngOnInit() {
  }

  calcPercentage(a, b) {
    let r = (a / b) * 100;
    if (r <= 33.3) {
      return 1;
    }
    if (r > 33.3 && r <= 66.6) {
      return 2;
    }
    if (r > 66.6) {
      return 3;
    }
  }
  
  getOutputPercentageColor(a, b) {
    let r = (a/b) * 100;
    if (r <= 33.3) {
      return 'badge badge-danger';
    }
    if (r > 33.3 && r <= 66.6) {
      return 'badge badge-warning';
    }
    if (r > 66.6) {
      return 'badge badge-success';
    }
  }
  
  getPercentage(a, b) {
    let r = (a / b) * 100;
    return r.toFixed(2)
  }
  
  getOutputPercentage(a, b, c) {
    let sub = c - a;
    let r = (b / sub) * 100;
    return r.toFixed(2)
  }

  displayProjectDetails(data){
    const initialState = {
      data: data
    }
    const modalRef = this.modalService.show(ProjectDetailsModalComponent, { class: 'modal-lg', initialState });
  }

  updateFilter(event) {
    let val = event.target.value;

    // filter our data

    const temp = this.temp.filter((d: any) => {
      let title: any = d.title
      let output: any = d.output.title
      let status = d.status
      return title.toLowerCase().indexOf(val) !== -1 || !val || output.toLowerCase().indexOf(val) !== -1 || status.toLowerCase().indexOf(val) !== -1;
    });

    this.projects = temp;
  }

  changePageSize(e) {
    if (e.target.value == 'all') {
      this.pageSize = this.projects.length
    } else {
      this.pageSize = parseInt(e.target.value)
    }

  }
}
